.c-recorder {
  .c-recorder__select {
    white-space: nowrap;
  }

  .c-recorder__select-input-container {
    display: flex;
    flex: 1;
  }

  .c-recorder__select-input {
    padding: 3px 3px 3px 0px;
    border: none;
    border-radius: 0;
    background-color: #fbfbfb;
    width: 100%;
  }

  .c-recorder__select-icon-holder {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #e8e8e8;
    border-bottom: 1px solid #bdbcbc;
  }
}
