.c-player__controls {
  width: 100%;
  display: table;
  height: 25px;
  background-color: #f9f9f9;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
  transition: all .3s;

  .c-player__custom-action {
    display: table-cell;
    vertical-align: middle;
    width: 30px;
    position: relative;
  }

}