.review-comment {
  margin: 16px 0;
  display: flex;
  flex-direction: row;
  padding-left: 6px;

  .comment-deleted {
    font-style: italic;
  }
}

.review-comment-synctime {
  margin-right: 6px;
}

.review-comment-container p, .review-comment p {
  margin: 0 0 2px 0;
}

.review-comment-actions {
  white-space: nowrap;
  align-self: center;
}

.review-comment-text {
  display: inline-block;
  align-self: center;
  margin: 0;
  padding: 2px 0;
  white-space: pre-wrap;
  word-break: break-word;
}

.review-comment-text p {
  margin: 0
}

.review-comment-media {
  display: flex;
}

.review-comment-media-container {
  display: block;
}

.review-comment-media-processing, .review-comment-media-player {
  width: 100%;
  min-height: 140px;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-basis: 50%;
}

.review-comment-media-processing * {
  color: white;
}

.review-comment-media-text {

}

.review-comment-media-container .review-comment-actions {
  align-self: flex-end;
}