@import 'common/styles/theme.scss';

.auto-analysis-transcript-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 8px;
}

.auto-analysis-transcript-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  .auto-analysis-transcript-title {
    margin: 0;
    font-size: 16px;
    text-transform: uppercase;
  }

  .auto-analysis-transcript-toolbar {
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    width: 100%;

    .auto-analysis-transcript-toolbar-actions-container {
      display: flex;
      justify-content: flex-end;
      flex: 1;
    }
  }

  .auto-analysis-context-select-field {
    min-width: 200px;
    border: 2px solid rgba(0,0,0,.1);
    border-radius: 8px;

    .md-select-field {
      border-radius: 6px;
    }
  }
}

.auto-analysis-transcript-contents {
  display: flex;
  flex-direction: row;
  max-width: 1024px;

  &.auto-analysis-transcript-contents-small-width {
    flex-direction: column;
  }

  .auto-analysis-transcript-contents-transcript {
    overflow-y: auto;
    padding: 0 2px;
  }
}

.auto-analysis-transcript-fragment--selected {
  padding: 0 2px;
  margin: 0 -2px;
  border-radius: 4px;
  background-color: themed-color('selection-control-selected');
  color: white;
  -webkit-transition: background-color 700ms linear;
  -ms-transition: background-color 700ms linear;
  transition: background-color 700ms linear;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
