.c-file-uploader {
  .c-file-uploader__file {

  }

  .c-file-uploader__file.c-file-uploader__file--queued {

  }

  .c-file-uploader__file.c-file-uploader__file--uploading {
    background-color: #b4eab9;
  }

  .c-file-uploader__file.c-file-uploader__file--success {
    background-color: #ececec;
  }

  .c-file-uploader__file.c-file-uploader__file--paused {
    background-color: #ead2b4;

  }

  .c-file-uploader__file.c-file-uploader__file--retry {
    background-color: #feffaf;
  }

  .c-file-uploader__success-icon {
    color: #0a8415;
  }

  .c-file-uploader__remove-icon {
    cursor: pointer;

    &:hover {
      color: black;
    }
  }

  .c-file-uploader__error-icon {
    color: #d66f74;
  }
}