.leftToRight-appear {
  opacity: .01;
  position: relative;
  left: -50px;
}

.leftToRight-appear.leftToRight-appear-active {
  opacity: 1;
  transition: all 150ms ease-in;
  position: relative;
  left: 0;
}

.leftToRight-exit {
  opacity: 1;
  position: relative;
  right: 0;
}

.leftToRight-exit.leftToRight-exit-active {
  opacity: .01;
  transition: all 150ms ease-in;
  position: relative;
  right: -50px;
}
