.c-recorder {
  .c-recorder__iconed-button-holder {
    display: flex;
  }

  .c-recorder__iconed-button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: transparent;
    padding: 0;
    width: 25px;
    cursor: pointer;
    height: 100%;

    &:hover {
      background-color: rgba(0, 0, 0, .1);
    }
  }

}
