@import "common/styles/bongo-vars.scss";
@import 'common/styles/theme.scss';

.md-dialog-container.md-overlay {
    z-index: 999;
}

.dialog {
    background: themed-color('surface-background');
    
    h2 {
        margin-bottom: 0;

        &.md-title {
            padding: 16px;
        }
    }

    &.full-page {
        display: flex;
        flex-direction: column;
    }

    .dialog-mobile-contents {
        flex : 1;
    }

    .dialog-mobile-actions {
        width: 100%;
        display: flex;
        padding: 8px;
        justify-content: flex-end;
    }
}

.dialog-header {
    display: flex;
    align-items: center;

    &.with-close-button {
        justify-content: flex-end;
    }
   
    &.with-text {
        justify-content: flex-start;
    }

    .dialog-title {
        flex: 1;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: inherit;
        margin: 0;
    }
}

.md-dialog--full-page.full-page {
    background-color: themed-color('page-background');

    @media screen and (max-width: 320px) {
        .md-toolbar-relative {
            margin-top: 56px;
        }
        .md-toolbar {
            height: 56px;
        }
    }
}

.dialog-contents {
    padding: 0 24px 16px 24px;

    &.full-page {
        padding: 0;

        .page-toolbar {
            h2 {
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        &.has-actions {
            display: flex;
            flex-direction: column;
            flex: 1;
        }
    }
}

.add-video-dialog {
    .dialog-contents.full-page {
        .add-video-contents {
            padding-top: 16px;
        }
    }
}

.notification-dialog {
    position: absolute;
    right: 30px;
    background-color: #fafafa;
    width: auto;
}

.notification-dialog-title {
    border-bottom: 1px solid #ddd;
    padding: 8px 18px 8px 18px !important;
    line-height: 32px !important;
}

.preview-dialog {
    display: flex;
    flex-direction: column;
    margin: 0 auto;

    .dialog-content {
        padding: 0;
        overflow: auto;

        .dialog-contents {
            padding: 0 16px 16px;
        }
    }
}

.preview-dialog.mobile-preview {
    .preview-contents {
        background-color: black;
        display: flex;
        flex-direction: column;
    }

    .dialog-media {
        align-items: center;
        display: flex;
    }
}

.preview-dialog .button-icon.md-btn--dialog {
    min-width: auto !important;
    height: 40px;
    width: 40px;
}

.dialog-cross {
    position: absolute;
    top: 10px;
    right: 10px;
}

@media all and (max-width: 550px) {
    .dialog.dialog-fullscreen {
        height: 100vh;
        width: 100% !important;
        display: flex;
        flex-direction: column;
    }

    .dialog-fullscreen .dialog-content {
        flex: 1;
    }
}

.unsupported-browser-dialog {
    max-width: 740px;

    .dialog-contents {
        font-size: 14px;

        .mobile-title {
            margin-top: 8px;
            white-space: normal;
            margin-bottom: 24px;
        }

        .feature-restriction-msg {
            margin-bottom: 24px;
            color: #666;
            font-size: 16px;
            font-weight: bold;
        }
    }
}

.supported-browsers {
    &.browser-options {
        display: flex;
        flex-wrap: wrap;
       
        div:first-child {
            flex: 1;
        }

        .browser-logo {
            height: 20px;
            margin: 0 4px 0 4px;
        }
    }
}

.env-dialog {
  .dialog {
    width: 100%;
    max-width: 750px;
  }

  .dialog-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 16px;

    .env-dialog-entries {
      display: flex;
      flex-direction: column;
      flex: 1;

      .env-dialog-entry {
        display: flex;
        flex-direction: row;
        margin-bottom: 6px;

        .env-dialog-entry-label {
          width: 100px;
          font-weight: bold;
          text-align: right;
          margin-right: 4px;
        }

        .env-dialog-entry-value {
          word-break: break-all;
          flex: 1;
        }
      }
    }
  }

  .env-dialog-loglevels {
    display: flex;
    flex-direction: column;
  }

  .env-dialog-loglevel {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .env-dialog-actions {
    display: flex;
    justify-content: center;
  }

  .env-dialog-tabs {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: 8px;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgba(0, 0, 0, .3);

    .env-dialog-tab {
      display: flex;
    }
  }
}
