.meeting-cohort-dialog {
  width: 90%;
  max-width: 1200px;
  max-height: 75%;

  &.meeting-cohort-dialog--empty {
    max-width: 600px;
  }

  .dialog-content {
    display: flex;
    flex-direction: column;
  }

  .dialog-contents {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .meeting-cohort-table-container {
    overflow: auto;
    flex: 1;
  }

  .meeting-cohort-table {
    overflow: unset;
  }

  .meeting-cohort-column, .meeting-cohort-column-header {
    padding-left: 6px;
    padding-right: 6px;
    overflow: hidden;
  }

  .meeting-cohort-column--centered, .meeting-cohort-column-header--centered {
    text-align: center;
  }

  .meeting-cohort-status-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-width: 125px;
  }

  .meeting-cohort-actions-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .meeting-cohort-message-container {
    display: flex;
    flex-shrink: 0;
  }

  .meeting-cohort-message-container--centered {
    justify-content: center;
  }

  .meeting-cohort-info-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    flex-shrink: 0;
    margin-bottom: 16px;

    h3 {
      margin: 0;
      font-size: 18px;
      white-space: pre-wrap;
      text-align: center;
    }

    p {
      margin: 0;
    }
  }
}

.meeting-cohort-errors-container {
  margin-top: 16px;
}

.meeting-cohort-attribute-container {
  display: flex;
  align-items: center;
  margin: 0 12px;

  p {
    margin: 0 0 0 8px;
  }
}

.meeting-cohort-attribute-icon {
  // This fixes some box-model height calculation when using flex display (icon was setting it to 1)
  line-height: unset;
}

.card-top-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 16px;
}
