@import "common/styles/bongo-vars.scss";

.interactive-video-grade-form-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;

  h3, h4, h5, h6 {
    margin: 0;
  }
}

.interactive-video-grade-form {
  display: flex;
  flex-direction: column;

  .interactive-video-grade-form-row-container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .interactive-video-grade-form-row-separator {
    height: 1px;
    background: $bongo-color-secondary-gray-light;
    margin: 12px 0;
  }

  .interactive-video-grade-form-row-label {
    display: flex;
    justify-content: flex-end;
    flex-shrink: 0;
    flex-grow: 1;
    margin: 0 12px 0 0;
  }

  .interactive-video-grade-form-row-value-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 96px;

    .interactive-video-grade-form-row-value {
      flex-shrink: 0;
      text-align: center;
    }

    .interactive-video-grade-form-row-value-footer {
      i {
        font-size: 11px;
        color: $bongo-color-secondary-gray-light;
      }
    }

    .interactive-video-grade-form-row-value-footer-error {
      i {
        color: $bongo-color-avatars-red;
      }
    }
  }
}
