@import "../../styles";

.c-bbb-player {
  .c-bbb-player__thumbnails_holder {
    padding: 4px;
  }

  .c-bbb-player__thumbnails {
    display: flex;
    flex-direction: row;
    padding: 0;
    word-break: keep-all;
    white-space: nowrap;
    overflow-x: auto;
    height: 155px;
  }

  .c-bbb-player__thumbnail {
    outline: none;
    outline-offset: -5px;
    height: 100%;
    margin-left: 8px;
    display: flex;
    position: relative;
    cursor: pointer;

    &:first-child {
      margin-left: 0;
    }

    &:hover, &:focus {
      outline: 5px solid #dae0ea;

      .c-bbb-player__thumbnail-time {
        visibility: visible;

      }
    }
  }

  .c-bbb-player__thumbnail.c-bbb-player__thumbnail--active {
    outline: 5px solid #b4d8ea;

    .c-bbb-player__thumbnail-time {
      visibility: visible;

    }
  }

  .c-bbb-player__thumbnail-time {
    position: absolute;
    left: 0;
    bottom: 0;
    margin: 0;
    background-color: #3A87AD;
    font-weight: bold;
    color: white;
    display: flex;
    line-height: 1;
    padding: 4px;
    visibility: hidden;
  }

  .c-bbb-player__thumbnail-image {
    height: 100%;
    max-width: 150px;
    object-fit: contain;
    object-position: center;
  }

  @media (max-width: $full-width-breakpoint) {
    .c-bbb-player__thumbnails_holder {
      width: 100%;
    }
  }
}
