@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';
@import './SubmissionReviewVars.scss';

.submission-review-comments-container {
  max-height: $fixed-content-height;
  min-height: $fixed-content-height;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  padding: 0;

  &.no-media {
    padding: 8px;
    border-top: 1px solid #eee;
  }

  .submission-review-comments {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding: 16px;
    flex: 1;

    &.no-comments {
      align-items: center;
      justify-content: center;
    }
  }

  .submission-review-comment-submit-section {
    display: flex;
    margin-left: 16px;

    .add-video-comment-button {
      margin-right: 4px;
    }

    .add-video-comment-button:disabled .md-icon,
    .add-video-comment-button:disabled:hover .md-icon,
    .send-comment-button:disabled .md-icon,
    .send-comment-button:disabled:hover .md-icon {
      color: rgba(0, 0, 0, 0.24);
    }

    .comment-preset-dropdown {
      flex: 1;

      .comment-preset-dropdown-list {
        bottom: 48px;
        top: unset;
        padding: 0;
        width: 100%;

        .comment-preset-dropdown-item {
          .comment-preset-dropdown-item-tile {
            padding: 8px;
            height: auto;
          }

          .comment-preset-dropdown-item-text {
            .md-text {
              white-space: normal;
            }
          }
        }
      }
    }
  }
}
