@import 'common/styles/bongo-vars.scss';

.activity-toolset-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .activity-toolset-primary {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 0 24px 0;

    > div {
      margin-right: 12px;
    }

    > div:last-child {
      margin-right: 0;
    }
  }

  .activity-toolset-secondary {
    display: flex;
    margin: 0;
  }
}

.activity-auto-analysis-key-terms-container {
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .activity-auto-analysis-key-terms-paper {
    padding: 16px;
    max-width: 600px;
  }

  .activity-auto-analysis-key-terms-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 8px;

    .activity-auto-analysis-key-terms-header-title {
      margin: 0;
      font-size: 20px;
      margin-bottom: 4px;
    }
  }

  .activity-auto-analysis-desired-key-terms-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .activity-auto-analysis-desired-key-terms-actions {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px;

    .activity-auto-analysis-desired-key-terms-actions-input {
      margin-right: 16px;
      min-width: 160px;
    }
  }

  .activity-auto-analysis-key-term-validation-msg {
    display: flex;
    flex-direction: column;
    color: $bongo-color-avatars-red;
  }

  .activity-auto-analysis-desired-key-terms {
    margin-top: 12px;
  }
}

.activity-auto-analysis-container {
  > h2 {
    margin-bottom: 0;
  }
}
