@import 'common/styles/theme.scss';

@mixin dropzone-active {
  p {
    color: themed-color('button-flat-primary-contrast');
  }

  background-color: themed-color('button-flat-primary-contrast', .1);
}

.c-file-uploader {
  width: 100%;
  font-size: 14px;
  color: #222222;
  box-sizing: border-box;

  .c-file-uploader__header {
    padding: 5px 15px;
  }

  .c-file-uploader__instruction {
    text-align: center;
  }

  .c-file-uploader__instruction.c-file-uploader__instruction--uploading {
    line-height: 40px;
  }

  .c-file-uploader__instruction.c-file-uploader__instruction--pause {
    line-height: 40px;
  }

  .c-file-uploader__limitation {
    text-align: center;
    text-transform: uppercase;
    color: red;
  }

  .c-file-uploader__body {
    font-size: 11px;
  }

  .c-file-uploader__table-header {
    background: none repeat scroll 0 0 #777777;
    border-top: 1px solid #EEE;
    border-bottom: 1px solid #CDCDCD;
    padding: 8px 8px;
    color: #ffffff;
  }

  .c-file-uploader__upload-progress {
    margin: 1px 0 0 0;
  }

  .c-file-uploader__upload-progressbar {
    background: #eefbd9;
    height: 13px;
  }

  .c-file-uploader__upload-progress-strip {
    height: 100%;
    background: #CDEB8B;
    width: 0%;
    transition: all .3s;
  }

  .c-file-uploader__info {
    padding: 5px;
  }

  .c-file-uploader__info-field {
    display: inline-block;
    margin: 0 10px 0 0;
  }

  .c-file-uploader__info-field-icon {
    font-size: 20px;
  }

  .c-file-uploader__info-field-text {
    font-size: 15px;
    vertical-align: top;
    padding: 0 0 0 4px;
    display: inline-block;
  }

  .c-file-uploader__drop-area {
    margin: 2px;
    border: 3px dashed #ddd;
    height: 100px;
    background-color: #f7f7f7;
    text-align: center;
    cursor: pointer;
    position: relative;

    .c-file-uploader__drop-area-text {
      text-transform: uppercase;
      letter-spacing: 3px;
      font-weight: bolder;
      font-size: 20px;
      color: #bdbcbc;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
      text-align: center;
    }

    &:hover, &:focus-within {
      @include dropzone-active();
    }
  }

  .c-file-uploader__drop-area--active {
    @include dropzone-active();
  }

  .file-uploader-choose-actions-container, .file-uploader-transfer-actions-container {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
   
    > div {
      margin-left: 8px;
    }
   
    > div:first-child {
      margin-left: 0;
    }
  }

  .file-uploader-transfer-actions-container {
    margin-bottom: 8px;
  }

  .file-uploader-list--container {
    display: flex;
    flex-direction: column;
  }

  .file-uploader-list--paper {
    margin: 8px;
  }
}

.external-tool-transfer-container {
  .external-tool-transfer-message {
    text-transform: uppercase;
    text-align: center;

    background-color: #888;
    color: transparent;
    font-weight: bold;
    text-shadow: 0 2px 3px rgba(255, 255, 255, .5);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text; // NOSONAR (This may only be an issue with IE)
  }
}
