.feedback-wrapper {
  display: flex;
  flex-direction: row;
}

@media all and (max-width: 1000px) {
  .feedback-wrapper {
      flex-direction: column;
  }
}

.feedback-submit-container {
  display: flex;
  flex-direction: column;
}

.feedback-submit-section {
  display: flex;
  padding: 6px;
}

.feedback-video-container {
  display: flex;
  flex: 0 1 50%;
  align-items: flex-end;
  background-color: black;
  padding: 0 0 48px 0;
  flex-direction: column;
}

.feedback-video-container button {
  color: white;
}

.feedback-feed-container {
  display: flex;
  flex: 1;
  flex-direction: column;
  min-height: 132px;
}

.feedback-video-container .c-player {
  max-width: 500px;
  align-self: center;
}

.feedback-feed {
  flex: 1;
}

.feedback-text-dropdown {
  flex: 1;
}

.feedback-rating-container {
  margin: 0 auto;
  padding-bottom: 6px;
}

.feedback-text-dropdown-list {
  top: unset;
  bottom: 48px;
  padding: 0;
  width: 100%;

  .feedback-text-dropdown-item {
      .feedback-text-dropdown-item-tile {
          padding: 8px;
          height: auto;
      }
 
      .feedback-text-dropdown-item-text {
          .md-text {
              white-space: normal;
          }
      }
  }
}

.edit-review-item-dialog {
  .md-dialog {
      max-width: 500px;
      width: 100%;
  }
}