@import 'common/styles/bongo-vars.scss';

.peer-review-form {
  margin-top: 16px;
}

.peer-review-header {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.peer-review-header h2 {
  margin: 0;
  flex: 1;
}

.peer-review-other-options {
  display: flex;

  > * {
    flex: 1;
  }
}

.peer-review-required-amount {
  padding-right: 8px;
}

.peer-review-quantitative-type {
  padding-left: 8px;
}

.peer-review-options-container {
  padding: 16px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .graded-peer-review-options-container {
    display: flex;
    flex-direction: column;

    .graded-peer-review-checkbox-container {
      display: flex;
      flex-direction: row;

      .graded-peer-review-checkbox-label {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;

        .graded-peer-review-weight {
          margin: 0 4px;
          width: 40px;

          .md-text-field--margin {
            margin: 0;
          }

          .md-divider {
            margin: 0;
          }
        }
      }
    }

    .graded-peer-review-weight-error-message {
      margin-left: $bongo-button-icon-size;
    }
  }
}

.graded-peer-review-has-grade-message {
  padding: 0 0 16px 0;

  .status-message-content-text {
    font-weight: bold;
  }
}

@media all and (max-width: 900px) {
  .peer-review-other-options {
    flex-direction: column;
  }

  .peer-review-quantitative-type {
    padding-left: 0;
  }

  .peer-review-required-amount {
    padding-right: 0;
  }
}

.peer-review-datetime-message {
  margin: 8px 0 16px 0;
 
  .status-message-content-text {
      font-weight: bold;
  }
}
