@import 'common/styles/bongo-vars.scss';

.notification-item-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;

  .notification-item-indicator {
    width: 32px;
    height: 100%;
    margin: 0;
    background-color: $bongo-color-secondary-gray-light;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    &.notification-item-indicator--error {
      background-color: $bongo-color-avatars-red;
    }

    &.notification-item-indicator--success {
      background-color: $bongo-color-avatars-green;
    }

    &.notification-item-indicator--announcement {
      background-color: $bongo-color-avatars-orange;
    }

    .notification-item-icon {
      color: #FFF;
    }
  }

  .notification-item-content {
    flex: 1;
    padding: 8px;
    word-wrap: break-word;
    color: rgba(0, 0, 0, .87);
  }
}
