@import 'common/styles/bongo-vars.scss';

.c-recorder {
  background: #000;
 
  .c-audio-level {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    position: relative;

    .c-audio-level__strip {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      background: #437a3f;
    }

    .c-audio-level__label {
      position: absolute; // this allows the label to be on top of the green bar (if it is after it in the DOM)
      font-weight: bold;
      line-height: 24px;
      color: #827f7f;
      letter-spacing: 2px;
      text-transform: uppercase;
      margin: 0;
    }

  }
}
