@import 'common/styles/mixins.scss';
@import 'common/styles/bongo-vars.scss';

.margin-centered {
  float: none !important;
  margin: 0 auto !important;
}

.dashboard-card-body {
  position: relative; /* Fixes watermark icon overlaying text issues */
}

.dashboard-days-container {
  display: flex;
  flex-direction: column;
  align-items: baseline;
}

.dashboard-date-due {
  font-size: 14px;
}

.dashboard-card-grade {
  .dashboard-card-grade-contents {
    display: flex;
    flex: 1;

    .dashboard-contents {
      display: flex;
      flex: 1;
    }
  }
}

.grade-composite-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 100%;

  .grade-composite-row {
    display: flex;
    flex-direction: row;
    margin-bottom: 12px;

    .grade-composite-label {
      margin-right: 8px;

      h3 {
        font-size: 24px;
        line-height: 32px;
        margin: 0;
      }

      h4 {
        font-size: 18px;
        line-height: 32px;
        margin: 0;
      }
    }

    .grade-composite-value {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 24px;
      line-height: 32px;

      .grade-composite-value-footer {
        font-size: 11px;
        line-height: 11px;
        font-size: 11px;
        line-height: 11px;
        color: $bongo-color-secondary-gray-dark;
      }
    }
  }

  .grade-composite-row-overall {
    .grade-composite-value {
      font-size: 32px;
    }
  }

  .grade-composite-row-separator {
    height: 1px;
    background: $bongo-color-secondary-gray-light;
    width: 100%;
  }

  .grade-composite-row:last-child {
    margin-bottom: 0;
  }
}
