@import "../../styles";

.c-bbb-player {

  .c-bbb-player__presentation {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  .c-bbb-player__slides {
    position: absolute;
    height: 100%;
    max-width: 100%;
    width: 100%;
    left: 50%;
    top: 0;
    transform: translateX(-50%);

    svg {
      width: auto;
      height: 100%;
      max-width: 100%;

      foreignObject {
        overflow: visible;
      }
    }
  }

  @media (max-width: $full-width-breakpoint) {
    .c-bbb-player__presentation {
      display: block;
      width: 100%;
      order: 2;
      position: static;
    }

    .c-bbb-player__slides {
      position: static;
      width: 100%;
      height: auto;
      margin: 0 auto;
      transform: none;

      svg {
        width: 100%;
        height: auto;
      }
    }

  }
}