
.activity-log-container {
    font-size: 13px;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.notification-dialog .activity-log-container {
    padding-top: 4px;
    height: 50vh;
}

.activity-log-messages {
    flex: 1;
    padding: 8px;
    overflow: auto;
}

.activity-log-item {
    display: flex;
    align-items: center;
}

.activity-log-item-contents {
    margin: 0 8px 0 8px;
    padding: 8px;
    color: white;
    word-break: break-all;
    white-space: pre-line;
}

.chat-message-item-contents p {
    color: white;
}

.activity-log-item-avatar {
    flex-shrink: 0;
}
