@import "common/styles/mixins.scss";

.c-double-player {
  width: 100%;

  .c-double-player__streams-holder {
    position: relative;
    overflow: hidden;
  }

  .c-double-player__streams-holder.c-double-player__streams-holder--column {
    display: flex;
    align-items: center;
    .c-double-player__stream-holder {
      position: static;
      width: 50%!important;
    }
    .c-double-player__stream-holder.c-double-player__stream-holder--active {
      order: 2;
    }
    .c-double-player__stream-holder:not(.c-double-player__stream-holder--active) {
      order: 1;
    }
    .c-double-player__resizer {
      display: none;
    }
  }

  .c-double-player__stream {
    height: auto;
    @include noselect;
  }

  .c-double-player__stream--main {
    width: 100%;
  }

  .c-double-player__stream-holder {

  }

  .c-double-player__toggle-view-icon {
    color: #545454;
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);

    &:hover {
      color: rgb(0, 188, 212);
    }
  }

  .c-double-player__stream-holder--active {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20%;
    cursor: pointer;
    z-index: 1;
  }

  .c-double-player__resizer {
    position: absolute;
    width: 30px;
    height: 100%;
    top: 0;
    right: -20px;
    cursor: ew-resize;

  }

  .c-double-player__stream--second {
    width: 100%;
    height: auto;
  }

  .c-double-player__stream--second:hover {
    outline: 4px solid rgba(255, 255, 255, .3);
  }

  .c-player .c-player__controls {
    display: none;
  }

  &:hover {
    .c-player__controls {
      transform: translateY(0);
    }
  }

  .c-player__controls {
    z-index: 2;
  }

}

.c-double-player-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  max-height: 100%;

  &:fullscreen {
    height: 100%;
    .c-player__controls {
      transform: translateY(0);
    }
    .c-double-player {
      height: 100%;
    }
    .c-double-player__streams-holder {
      height: 100%;
    }
    .c-double-player__stream-holder:not(.c-double-player__stream-holder--active),
    .c-double-player__streams-holder.c-double-player__streams-holder--column .c-double-player__stream-holder {
      height: 100%;
      .c-double-player__stream {
        height: 100%;
      }
      .c-player {
        height: 100%;
        .video-js, .c-player__video-wrapper{
          width: 100% !important;
          height: 100% !important;
          padding-top: 0 !important;
        }
      }
    }
  }
}

.video-player-play-button--dual-stream {
  z-index: 1;
}
