@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.qa-work-in-progress-container {
  &.qa-work-in-progress--standalone {
    margin: 16px;
  }

  .qa-progress-card {
    .qa-question-card-title {
      padding-bottom: 6px;
    }

    .qa-progress-card-body {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 0;

      .qa-recorder-container {
        display: flex;
        flex: 1;
        background-color: black;

        &.has-playback-media {
          &.record-reaction {
            position: relative;
          }
        }

        .no-playback-media {
          display: flex;
          flex: 1;
          .c-recorder {
            flex: 1;
          }
        }

        .qa-question-playing {
          &.has-playback-media {
            position: absolute;
            visibility: hidden;

            &.record-reaction {
              visibility: visible;
              position: absolute;
              height: 100px;
              width: 100px;
              top: 0;
              left: 0;
              display: flex;

              .c-recorder {
                video {
                  height: 100px;
                  width: 100px;
                }
              }

              .c-recorder__controls-bar {
                display: none;
              }

              .c-recorder__time-ticker {
                display: none;
              }

              .c-recorder .c-recorder__controls-bar {
                padding: 0 !important;
                min-height: 0;
              }

              .c-recorder__stream-holder {
                min-height: 0;
              }
            }
          }

          &.record-reaction {
            &.media-loading {
              visibility: hidden;
            }
          }
        }

        .c-recorder {
          video {
            max-height: 50vh;
          }
        }
      }
    }
  }

  &.full-screen-question {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 999;
    background-color: white;

    .qa-progress-card {
      height: 100%;

      .qa-progress-card-body {
        display: flex;
        flex-direction: column;

        .qa-recorder-container {
          display: flex;
          flex: 1;
        }
      }
    }

    &.landscape {
      .qa-progress-card {
        display: flex;
        flex-direction: column;

        .qa-progress-card-body {
          display: flex;
          flex-direction: row;
          align-items: center;
          flex: 1;
          width: 100%;
          margin-bottom: 16px;

          .qa-recorder-container {
            max-height: inherit;

            video {
              max-height: inherit;
            }
          }
        }
      }
    }
  }
}

.qa-progress-card-body {
  width: 100%;
  display: flex;
}

.qa-progress-container-contents {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;

  .qa-start-question-action-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .status-message {
      padding: 8px 0;
    }

    .qa-button-start-question {
      margin-top: 8px;
    }

    .qa-start-question-recorder-status-container {
      margin-bottom: 24px;
    }

    .qa-start-question-recorder-status-message {
      text-align: center;
    }

    .qa-start-question-recorder-status-items {
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      justify-content: center;
      flex-wrap: wrap;
    }
  }
}

.qa-start-question-recorder-status-item {
  $icon-size: 48px;

  display: flex;
  flex-direction: column;
  margin: 0 16px;
  align-items: center;

  .qa-start-question-recorder-status-item-title {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    max-width: 100px;
    overflow-wrap: break-word;
  }

  .status-item-icon-container {
    position: relative;
    width: $icon-size;
    height: $icon-size;
  }

  .status-item-icon {
    position: absolute;
    font-size: $icon-size;
    opacity: 1;
    transition: all .5s;

    // &.status-item-icon-type {
    // }

    &.status-item-icon-state {
      color: themed-color('status-success');
    }

    @keyframes status-item-icon-pending {
      from {
        opacity: 1;
      }
      to {
        opacity: .5;
      }
    }

    &.status-item-icon--pending {
      animation-name: status-item-icon-pending;
      animation-duration: .75s;
      animation-iteration-count: infinite;
      animation-direction: alternate;
      animation-timing-function: ease-in;
    }

    &.status-item-icon--hidden {
      animation-name: none;
      opacity: 0;
    }
  }
}

.qa-ready-msg-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.qa-player-container {
  width: 100%;
}

.qa-question-contents {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 24px;
  p {
    white-space: pre-wrap;
  }
}

.qa-activate-recorder-container {
  display: flex;
  position: relative;
  flex: 1;
  background-color: #ccc;
  padding-top: 75%; // allows width to grow and maintain 4:3 aspect ratio

  .qa-activate-recorder-contents {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .init-video-icon {
      color: white;
      font-size: 150px;
    }

    .activate-recorder-button {
      span {
        font-weight: bold;
        padding-left: 8px;
      }

      .activate-recorder-icon {
        color: white;
      }
    }
  }
}

@media all and (max-width: 650px) {
  .qa-work-in-progress-container .qa-progress-card .qa-progress-card-body {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
}

.qa-ios-not-safari-message {
  padding: 20px;
}

.qa-started-at div:first-child {
  display: flex;
  align-items: center;
}

.qa-note-error,
.qa-note-warning,
.qa-note-info {
  margin-bottom: 16px;
  max-width: 450px;
}

.confirm-open-new-window-dialog {
  .closeable-dialog-children-container {
    max-width: 300px;
    align-items: center;
    justify-content: center;

    p {
      text-align: center;
    }
  }
}
