@import 'common/styles/theme.scss';

$color: #d5122c;
$color-contrast: #fff;
$color-disabled: #ccc;
$color-disabled-contrast: #555;

.bongo-main {
  .button.recorder-start-button {
    @include create-button-skin($color, $color-contrast, $color-disabled, $color-disabled-contrast);
    @include create-ink-skins($color);

    height: unset;
    font-size: 20px;
    padding: 12px 16px;
    border-radius: 24px;

    .recorder-start-button-icon {
      margin-right: 6px;
    }
  }
}
