@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.status-message {
  display: flex;
  flex-direction: row;

  .status-message-icon {
    margin-right: 8px;
  }

  .status-message-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: $bongo-primary-text-color;

    .status-message-content-text {
      margin: 0;
      color: inherit;
    }

    &.status-message-content--error-color {
      color: themed-color('status-error');
    }

    &.status-message-content--info-color {
      color: themed-color('status-info');
    }

    &.status-message-content--success-color {
      color: themed-color('status-success');
    }

    &.status-message-content--warning-color {
      color: themed-color('status-warning');
    }

    &.status-message-content--restricted-color {
      color: themed-color('status-restricted');
    }

    &.status-message-content--unknown-color {
      color: themed-color('status-unknown');
    }
  }
}
