@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.content-list-item-actions-menu {
  &.content-list-item-actions-menu--hidden {
    visibility: hidden;
  }
}

.content-list-item--processing,
.content-list-item--processing-error {
  .content-list-item-name {
    flex-direction: column;

    .content-list-item-name--desc {
      font-style: italic;
    }

    .content-list-item-name--desc-name {
      font-weight: bold;
    }
  }
}

.content-list-item--processing-error {
  .content-list-item-icon {
    .content-list-item-type-icon.font-icon {
      color: themed-color('status-error');
    }
  }
}

.content-list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 4px;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  width: 100%;

  &:focus,
  &:focus-within {
    background-color: rgba(0, 0, 0, 0.05);
    outline: none;
  }

  &:last-child {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }

  &:hover {
    .content-list-item-actions {
      &.content-list-item-actions--secondary {
        display: flex;
      }
    }
  }

  &.animate-video-playback {
    .c-player {
      position: absolute;
      z-index: 1000;
      background-color: black;
      animation: 2s linear forwards grow infinite;
    }

    @keyframes grow {
      from {
        left: 30;
      }
      to {
        top: 0;
        left: 0;
        width: 100%;
        height: 360px;
      }
    }
  }

  .content-list-item-icon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin: 0;

    .content-list-item-type-icon {
      color: #757575;
      font-size: 32px;
    }
  }

  .content-list-item-details {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex: 1;
  }

  .content-list-item-name {
    display: flex;
    flex: 1;
    overflow: hidden;
    margin: 0 8px;

    .content-list-item-name--label {
      flex: 1;
      color: #757575;
      display: inline-block;
      position: relative;
      // white-space: nowrap;
      word-break: break-all;
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: left;
      font-weight: bold;
      margin-bottom: 0;
    }
  }

  .content-list-item-actions {
    display: flex;
    flex-direction: row;
    align-items: center;

    &.content-list-item-actions--primary {
      margin-right: 2px;
    }

    .item-sep {
      width: 2px;
      background-color: rgba(0, 0, 0, 0.2);
      align-self: stretch;
      margin: 4px;

      &.item-sep--hidden {
        visibility: hidden;
      }
    }
  }
}

@media all and (max-width: 400px) {
  .content-list-item {
    &.content-list-item--has-video-poster {

      .content-list-item-name--label {
        text-align: center;
      }

      .content-list-item-details {
        flex-direction: column;
      }
    }
  }
}

