@import '../../styles';
@import 'common/styles/theme.scss';

$block-height: 150px;

.c-bbb-player {
  .c-bbb-player__chat {
    display: flex;
    min-width: 215px;
    padding: 0;
    height: $block-height;
  }

  .c-bbb-player__download-chat-log {
    position: absolute;
    top: 2px;
    right: 20px;
  }

  .c-bbb-player__chat-message {
    padding: 5px;
  }

  .c-bbb-player__chat-message-text {
    margin: 0;
    word-break: break-all;
  }

  .c-bbb-player__chat-message-name {
    font-weight: bold;
    margin: 0 5px 0 0;
  }

  .c-bbb-player__chat-message-link {
    word-break: break-all;
    font-weight: bold;
  }

  .c-bbb-player__chat-no-data {
    position: absolute;
    width: calc(100% - 20px);
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    color: #dadada;
    text-transform: uppercase;
    font-size: 20px;
    letter-spacing: 2px;
  }

  .c-bbb-player__chat-wrapper {
    display: flex;
    flex: 1;
    height: $block-height;
  }

  .c-bbb-player__chat-list {
    flex: 1;
    background-color: #f9f9f9;
    margin: 0;
    border: 1px solid #dadada;
    border-left: none;
    position: relative;
  }

  .c-bbb-player__chat-messages-holder {
    height: $block-height;
    overflow-y: scroll;
    padding: 10px;
  }

  .c-bbb-player__chat-actions {
    width: 50px;
    display: flex;
    flex-direction: column;
    > * {
      flex: 1;
    }
  }

  .c-bbb-player__chat-action {
    background-color: transparent;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    border-right: 1px solid #dadada;

    &:hover .c-bbb-player__chat-action-icon {
      color: #00bcd4;
    }
  }
  .c-bbb-player__chat-action:first-child {
    top: 0;
  }
  .c-bbb-player__chat-action:last-child {
    bottom: 0;
  }

  .c-bbb-player__chat-action.c-bbb-player__chat-action--active {
    background-color: #f9f9f9;
    border: 1px solid #dadada;
    border-right: 0;
    cursor: default;

    &:hover .c-bbb-player__chat-action-icon {
      color: themed-color('page-contrast');
    }
  }

  .c-bbb-player__chat-action-icon {
    font-size: 20px;

    color: themed-color('page-contrast');
  }
}
