@import 'common/styles/theme.scss';

.loader-overlay {
  background: themed-color('page-overlay-background');
  bottom: 0;
  left: 0;
  opacity: 1;
  position: fixed;
  right: 0;
  top: 0;
  transition-duration: .15s;
  transition-property: opacity;
  z-index: 16;
}

:root {
  --bongo-spinner-size: 48px;
  --bongo-spinner-thickness: 5px;
  --bongo-spinner-contrast-color: #{themed-color('page-loader-contrast')};
  --bongo-spinner-background-color: #{themed-color('page-loader-background')};
}

.loader-spinner, .embedded-loader-spinner {
  position: absolute;
  font-size: 4px;
  text-indent: -9999em;
  border-top: var(--bongo-spinner-thickness) solid var(--bongo-spinner-contrast-color);
  border-right: var(--bongo-spinner-thickness) solid var(--bongo-spinner-contrast-color);
  border-bottom: var(--bongo-spinner-thickness) solid var(--bongo-spinner-contrast-color);
  border-left: var(--bongo-spinner-thickness) solid var(--bongo-spinner-background-color);
  left: 50%;
  top: 50%;
  transform: translateZ(0);
  animation: load 1.1s infinite linear;
  display: block;
  border-radius: 50%;
  width: var(--bongo-spinner-size);
  height: var(--bongo-spinner-size);
}

.embedded-loader-spinner {
  position: relative;
  top: calc(var(--bongo-spinner-size) / 2);
  left: calc(var(--bongo-spinner-size) / 2);
}

.processing-spinner {
  display: flex;
  position: relative;
  transform: translateZ(0);
  animation: load 5s infinite linear;
}

@keyframes load {
  0% {
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
  }

  100% {
    transform: translateX(-50%) translateY(-50%) rotate(360deg);
  }
}
