@import 'common/styles/mixins.scss';
@import 'common/styles/theme.scss';

html,
body {
  background-color: themed-color('page-background');
}

.main-wrap,
.c-page,
.main-container,
.container,
.content,
.main-logo-wrap,
.powered-by,
.page-heading-contents {
  @include clearfix;

  clear: both;
  float: none;
  min-height: 1px;
  height: auto !important;
  height: 1px;
}

.main-wrap,
.page,
.c-page,
.main-container,
.container,
.content,
.page-heading-contents {
  display: block;
}

.main-wrap,
.page,
.c-page {
  // prevent horizontal scrolling
  background: none;
  margin: 0;
  padding: 0;
}

.page {
  background-color: themed-color('page-background');
}

.container,
.content,
.main-container,
.main-logo-wrap,
.powered-by,
.page-heading-contents {
  margin: 0 auto;
}

.container,
.main-container,
.page-heading-contents {
  padding: 0 8px;
}

@media all and (min-width: 501px) {
  .container,
  .main-container,
  .page-heading-contents {
    padding: 0 24px;
    min-width: 300px;
  }
}

@media all and (min-width: 768px) {
  .container,
  .main-container,
  .page-heading-contents {
    padding: 0 32px;
    min-width: 428px;
    max-width: 1366px;
  }
}

.page {
  min-height: 100vh;

  .container {
    width: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .content {
    padding: 16px 0;
    flex: 1;
    width: 100%;
  }

  .content-title-description {
    font-size: 14px;
    margin-top: 8px;
  }
}

.powered-by {
  text-align: center;
}

.powered-by-logo {
  display: flex;
  width: 160px;

  &.powered-by-logo-small {
    // powered by logo 10% smaller for Toolwire
    width: 144px;
  }
}

.page-heading {
  background-color: themed-color('page-toolbar-background');
  color: themed-color('page-toolbar-contrast');
  height: 220px;
  position: absolute;
  width: 100%;
}

.content-title-container {
  width: 100%;

  .content-title {
    display: flex;
    width: 100%;
  }
}

.content-title-header, .page-heading-title {
  font-size: themed-style('page-h1-font-size');
  font-weight: themed-style('page-h1-font-weight');
  line-height: themed-style('page-h1-line-height');
  color: themed-color('page-h1');
}

.content-header-info {
  flex: 1;
}

.content-header-content-right {
  align-self: flex-end;
}

.content-title h1,
.content-title p {
  margin: 0;
}

.page-heading-contents {
  padding-top: 64px;
}

.page-heading h1 {
  font-size: 32px;
  color: themed-color('button-raised-primary-contrast');
}

.page .toolbar {
  box-shadow: none;
}

.page .content-title {
  position: relative;
  color: themed-color('page-toolbar-contrast');
}

.page .content-title * {
  color: inherit;
}

.page .content-title button:disabled {
  opacity: .4;
}

.page-debug-outline {
  outline-style: none;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;

  &.page-debug-outline--active {
    outline-style: none;
  }

  &.page-debug-outline--active * {
    outline: 1px solid #88aa88;
    outline-offset: -1px; // Not supported on IE11
  }
}
