  .c-player__captions-button {
    display: table-cell;
    width: 30px;
    position: relative;
    vertical-align: middle;
  }

  .c-player__captions-icon {
    cursor: pointer;
    position: absolute;
    top:50%;
    left:50%;
    transform: translateY(-50%) translateX(-50%);

    &:hover {
      color: rgb(0, 188, 212);
    }
  }

  .c-player__captions-icon.c-player__captions-icon--active {
    color: rgb(0, 188, 212);
  }

  .c-player__captions-list {
    background-color: #f9f9f9;
    position: absolute;
    top:0;
    right: 0;
    transform: translateY(-100%);
  }

  .c-player__captions-list-item {
    padding: 10px;
    cursor: pointer;

    &:hover {
      background-color: #bdbdbd;
    }
  }

  .c-player__captions-list-item.c-player__captions-list-item--active {
    background-color: #bdbdbd;
  }
