
.reflection-questions-config-item {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding-top: 8px;

  .md-text-field--margin {
    margin: 0;
  }

  .reflection-questions-config-item-index {
    margin: 0 8px 0 0;
    width: 24px;
    display: flex;
    justify-content: flex-end;
    font-size: 14px;
  }

  .reflection-questions-config-item-text {
    margin-right: 16px;
  }

  .reflection-questions-config-item-icons {
    display: flex;
    align-items: center;

    .reflection-questions-config-item-icons-reorder {
      display: flex;
    }
  }

  .reflection-questions-config-item-position {
    min-width: 24px;
    margin-right: 8px;
    display: flex;
    flex-direction: column;

    .reflection-questions-config-item-position--up,
    .reflection-questions-config-item-position--down {
      width: 24px;
      height: 24px;
      padding: 0;
    }
  }
}
