@import 'common/styles/theme.scss';

.peer-review-summary-card {
  .peer-review-summary-card-body {
    display: flex;
    flex-direction: column;
    align-items: center;

    .peer-review-summary-list-placeholder {
      padding: 24px;
      margin-bottom: 16px;
      font-weight: bold;
      font-size: 16px;
    }
  }

  .peer-review-summary-title {}

  .card-title-left-content-children {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    align-items: center;
  }

  .peer-review-summary-title-info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex-shrink: 0;
    margin-left: 8px;
  }

  .peer-review-summary-grading {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px;
    width: 100%;
    max-width: 350px;

    .peer-review-summary-grading-label {
      font-size: 16px;
      line-height: 24px;
      font-weight: 400;
      color: rgba(0, 0, 0, .87);
      margin-bottom: 8px;
    }

    .peer-review-summary-grading-value {
      margin-left: 6px;
    }

    .peer-review-summary-grading-override {
      display: flex;
      flex-direction: column;
      align-items: center;

      .peer-review-summary-grading-form {
        display: flex;
      }

      .peer-review-summary-grading-actions {
        display: flex;
      }

      .peer-review-summary-grading-percent {
        margin: 0 4px;
        width: 40px;

        .md-text-field--margin {
          margin: 0;
        }

        .md-divider {
          margin: 0;
        }
      }
    }
  }

  .peer-review-summary-list {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    width: 100%;
    margin-bottom: 16px;

    .peer-review-summary-list-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 8px;
      min-height: 48px;

      .peer-review-summary-list-item-number {
        display: flex;
        justify-content: flex-end;
        width: 32px;
        margin-right: 8px;
      }

      .peer-review-summary-list-item-status {
        display: flex;
        width: 20px;
        margin-right: 8px;

        .peer-review-summary-list-item-status-icon {
          &.status-icon--complete {
            color: themed-color('status-success');
          }
        }
      }

      .peer-review-summary-list-item-content {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        flex: 1;

        .peer-review-summary-list-item-label {
          font-size: 14px;

          /* font-weight: bold; */
          margin-right: 16px;
          flex: 1;
          min-width: 100px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        .peer-review-summary-list-item-content-right {
          display: flex;
          align-items: center;

          .peer-review-summary-list-item-evaluation {
            display: flex;
            justify-content: center;
            margin-right: 16px;

            &.peer-review-summary-list-item-evaluation-fivestar {
              min-width: 150px;
            }

            &.peer-review-summary-list-item-evaluation-rubric {
              min-width: 100px;
            }

            .peer-review-summary-list-item-evaluation-placeholder {
              font-style: italic;
            }
          }

          .peer-review-summary-list-item-comments {
            display: flex;

            .peer-review-summary-list-item-comments-icon {}

            .peer-review-summary-list-item-comments-badge {
              border-radius: 8px;
              background-color: themed-color('status-info');
              color: white;
              font-size: 10px;
              font-weight: bold;
              min-width: 16px;
              line-height: 16px;
              padding: 0 4px;
              position: absolute;
              top: 4px;
              right: 4px;
              display: flex;
              align-items: center;
              justify-content: center;
              box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, .75);
            }
          }
        }
      }
    }
  }
}

.review-comments-dialog {
  width: 100%;
  max-width: 500px;
  height: auto;
  max-height: 80%;
  display: flex;
  flex-direction: column;

  .dialog-content {
    display: flex;
    flex-direction: column;
  }

  .dialog-contents {
    display: flex;
    flex-direction: column;
  }

  .comments-container {
    overflow-y: scroll;
    margin: 16px 0;
  }

  .student-selection-paper {
    .student-selection-select-field {
      padding: 0;
    }
  }

  &.mobile-view {
    max-width: 100%;
    max-height: 100%;

    .dialog-mobile-contents {
      padding: 16px;
      display: flex;
      flex-direction: column;
    }

    .student-selection-container {
      padding-top: 16px;
    }
  }

  .peer-review-comments-container {
    margin-bottom: 16px;

    .review-comment {
      margin: 0 0 8px 0;
    }

    .peer-review-label {
      margin: 0 0 4px 0;
      font-weight: bold;
    }
  }

  .peer-review-comments-container:last-child {
    margin-bottom: 0;
  }
}

.peer-review-due-date-container {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  align-self: flex-start;

  p {
    margin: 0 0 0 8px;
  }
}

.peer-review-grade-form-overall {
  display: flex;
  align-items: center;

  .peer-review-grade-form-overall-status-icon {
    margin-right: 8px;
  }
}

.peer-review-grade-form-peer {
  display: flex;
  align-items: center;

  .peer-review-grade-form-peer-status-icon {
    margin-right: 8px;
  }
}

.peer-review-grade-form-message {
  margin: 16px 0 0 0;
}
