.group-student-activity-form {
    margin: 0 0 24px 0;
}

.activity-toolset-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0 24px 0;


    .activity-toolset-primary {
        display: flex;
        flex-direction: row;
        align-items: center;

        > div {
            margin-right: 12px;
        }

        > div:last-child {
            margin-right: 0;
        }
    }

    .activity-toolset-secondary {
        display: flex;
        margin: 0;
    }
}

.finalize-group-date-container, .group-team-eval-container, .finalize-group-min-max-container {
    display: flex;
    flex-direction: row;
}

.finalize-group-date-container .finalize-group-date-datepicker,
.finalize-group-date-container .finalize-group-date-timepicker,
.group-team-eval-container .team-eval-selectfield,
.group-team-eval-container .team-eval-select-rubric-container,
.finalize-group-min-max-container .max_students_amount,
.finalize-group-min-max-container .min_students_amount
{
    flex: 1;
}

.team-eval-select-rubric-container {
    min-width: 50%;
}

.finalize-group-date-container .finalize-group-date-datepicker,
.finalize-group-min-max-container .min_students_amount,
.group-team-eval-container .team-eval-selectfield
{
    padding-right: 8px;
}

.finalize-group-date-container .finalize-group-date-timepicker,
.finalize-group-min-max-container .max_students_amount,
.group-team-eval-container .team-eval-rubric-selectfield
{
    padding-left: 8px;
}

.group-team-eval-container {
    align-items: center;
}

@media all and (max-width: 900px) {
    .finalize-group-date-container, .finalize-group-min-max-container, .group-team-eval-container  {
        flex-direction: column;
    }

    .team-eval-select-rubric-container {
        min-width: 100%;
    }

    .finalize-group-date-container  .finalize-group-date-datepicker,
    .finalize-group-min-max-container .min_students_amount,
    .group-team-eval-container .team-eval-selectfield
    {
        padding-right: 0;
    }

    .finalize-group-date-container  .finalize-group-date-timepicker,
    .finalize-group-min-max-container .max_students_amount,
    .group-team-eval-container .team-eval-rubric-selectfield
    {
        padding-left: 0;
    }

    .group-options-container {
        display: block;
        width: 100%;
        min-width: 200px;
    }

    .group-options-container label, .group-options-container input, .group-options-container .md-select-field {
        white-space: normal !important;
        max-width: 100%;
    }

    .group-options-container input, .group-options-container .md-select-field {
        min-height: 1px !important;
        height: auto !important;
    }
 
    .group-options-container input {
        line-height: 2.5;
        min-height: 1px !important;
        height: auto !important;
    }
}
