.c-meeting-invite-dialog {
  display: flex;
  flex-direction: column;

  .closeable-dialog-children-container {
    width: 650px;
    max-width: 100%;
  }

  .c-meeting-invite-dialog__invite-sections-holder {
    display: flex;
    flex: 1;
    overflow: hidden;
  }

  .c-meeting-invite-dialog__invite-section {
    display: flex;
    flex-direction: column;
    flex: 1;

    .list {
      flex: 1;
      overflow-y: auto;
    }
  }

  .meeting-invite-sync-warning-message {
    margin-top: 16px;
  }
}
