@import "common/styles/bongo-vars.scss";
@import "common/styles/mixins.scss";

.interactive-video-questions-dialog {
  max-width: 768px;
  width: 90%;
  display: flex;
  flex-direction: column;

  .interactive-video-questions-dialog-content {
    display: flex;
    flex-direction: column;
    flex: 1;

    .interactive-video-questions-dialog-content-form {
      .interactive-video-questions-dialog-top {
        flex: 1;
        display: flex;
        flex-direction: row;

        .interactive-video-questions-dialog-left {
          flex: 1;
          display: flex;
          flex-direction: column;
          align-items: flex-start;

          .interactive-video-questions-dialog-question-type {
            max-width: 175px;
          }
        }

        .interactive-video-questions-dialog-right {
          margin-left: 16px;
          display: flex;
          flex-direction: column;
          align-items: center;

          .interactive-video-questions-dialog-time-container {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;

            .interactive-video-questions-dialog-time-set {
              &:focus {
                text-decoration: underline;
              }

              a {
                @include noselect;
              }
            }

            .interactive-video-questions-dialog-time {
              width: 128px;
              display: flex;
              flex-direction: column;
              align-items: center;

              hr {
                flex: 1;
                width: 100%;
              }

              textarea {
                text-align: center;
                overflow: hidden;
              }
            }
          }
        }
      }

      .interactive-video-questions-dialog-answers-container {
        padding: 16px;

        .interactive-video-questions-dialog-answers-header {
          display: flex;
          align-items: center;

          h4 {
            display: inline-block;
            margin: 0 8px 0 0;
          }
        }

        .interactive-video-questions-dialog-answers-list {
          .interactive-video-questions-dialog-answers-option {
            flex: 1;
            display: flex;
            flex-direction: row;
            height: 48px;
          }
        }
      }
    }

    .interactive-video-questions-dialog-validation-container {
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: flex-start;

      .interactive-video-questions-dialog-validation-icon {
        color: $bongo-color-avatars-red;
        font-size: 24px;
      }

      .interactive-video-questions-dialog-validation-errors {
        flex: 1;
        display: flex;
        flex-direction: column;

        h5 {
          flex: 1;
          color: $bongo-color-avatars-red;
          margin: 0 0 0 8px;
        }
      }
    }
  }
}
