@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.chat-container {
  padding-top: 48px;
  font-size: 13px;
  display: flex;
  flex-direction: column;
  height: 100vh;

  .chat-messages {
    flex: 1;
    padding: 8px;
    overflow: auto;

    .chat-message-item {
      display: flex;
      margin-top: 20px;

      &.deletable {
        p {
          margin: 0;
        }
      }

      .chat-message-actions {
        display: flex;
        align-items: center;

        .button-icon {
          $color: #fff;
          $color-disabled: #ccc;
          @include create-button-skin(
            transparent,
            $color,
            transparent,
            $color-disabled
          );
          @include create-ink-skins($color);
        }
      }

      .chat-message-avatar {
        flex-shrink: 0;
      }

      .chat-message-item-contents {
        word-break: break-all;
        white-space: pre-line;
        background: $bongo-color-blue;
        border-radius: 10px;
        color: #fff;
        margin: 0 10px 0 10px;
        padding: 10px;

        p {
          color: inherit;
        }
      }
    }

    .chat-message-item.reverse {
      flex-direction: row-reverse;
    }
  }

  .chat-actions {
    flex: 0 0 30px;
    background-color: white;
    padding: 8px;

    .chat-actions-contents {
      display: flex;

      .chat-actions-input {
        flex-grow: 1;
        margin-right: 20px;
        color: #555;
      }

      .chat-message-send-button {
        @include themed-button('button-flat', 'base');
      }
    }
  }
}

.notification-dialog .chat-container {
  padding-top: 4px;
  height: 50vh;
}
