@import 'common/styles/theme.scss';
$normal-rubric-width: 170px;
$normal-rubric-height: 100px;
$mobile-rubric-width: 120px;
$mobile-rubric-height: 100px;

.c-rubric {
  font-size: 15px;
  width: 100%;
  max-height: 100%;
  overflow: auto;
  position: relative;

  .c-rubric__table {
    border-spacing: 0;
    display: table;
    border-collapse: collapse;
  }

  .c-rubric__table.c-rubric__table--fixed-column {
    position: absolute;
    left: 0;
    top: 0;
    width: auto;
    z-index: 1;
  }

  .c-rubric__table.c-rubric__table--fixed-header {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
  }

  .c-rubric__fixed-corner {
    position: absolute;
    left: 0;
    top: 0;
    width: $normal-rubric-width;
    height: $normal-rubric-height;
    border: 1px solid #d2cfcf;
    padding: 5px;
    background-color: #f1eded;
    z-index: 1;
  }

  .c-rubric__row {
    display: table-row;
  }

  .c-rubric__cell {
    width: $normal-rubric-width;
    height: $normal-rubric-height;
    padding: 0;
    display: table-cell;
  }

  .c-rubric__cell-content {
    width: $normal-rubric-width;
    height: $normal-rubric-height;
    border: 1px solid #d2cfcf;
    padding: 4px;
    position: relative;
    transition: all .15s;
    background-color: white;
    cursor: pointer;
    outline: none;

    &:hover {
      background-color: #e1ffe4;
    }

    .c-rubric__description {
      letter-spacing: unset;
    }
  }

  .c-rubric__cell-content.c-rubric__cell-content--selected {
    background-color: #a9f9ad;
  }

  .c-rubric__cell-content.c-rubric__cell-content--head {
    background-color: #f1eded;
    text-align: center;
    cursor: default;
  }

  .c-rubric__cell-content.c-rubric__cell-content--first-column {
    background-color: #f1eded;
    text-align: center;
    cursor: default;
  }
  .c-rubric__remove-cell-button {
    position: absolute;
    bottom: -3px;
    right: 30px;
  }

  .c-rubric__add-cell-button {
    position: absolute;
    bottom: -3px;
    right: -2px;
  }

  .c-rubric__remove-row-button {
    position: absolute;
    bottom: 0;
    left: 30px;
  }

  .c-rubric__add-row-button {
    position: absolute;
    bottom: 0;
    left: -2px;
  }

  .c-rubric__short-icon {
    font-size: 24px;
    line-height: 15px;
    position: relative;
    top: 5px;
    color: #4fc3f7;
  }

  .c-rubric__points {
    position: absolute;
    bottom: 0;
    right: 3px;
    font-size: 17px;
    color: #4c4c4c;
    margin: 0;
    padding: 0;
  }

  .c-rubric__selected-row-icon {
    position: absolute;
    bottom: 0;
    right: 3px;
    color: #48804d;
  }

  .c-rubric__cell:focus * {
    background-color: aliceblue;
    outline: none;
  }

  .c-rubric__points-text {
    text-align: center;
    padding: 15px 0 0 0;
  }
}

.rubric-dialog {
  width: auto;

  .rubric-dialog__rubric-wrapper {
    overflow: hidden;
    max-height: calc(100vh - 200px);
    max-width: 100%;
  }

  .rubric-dialog__content {
    overflow: hidden;
  }

  .c-rubric {
    max-height: calc(100vh - 200px);
  }
}

@media screen and (max-width: 500px) {
  .c-rubric {
    .c-rubric__fixed-corner {
      width: $mobile-rubric-width;
      height: $mobile-rubric-height;
    }

    .c-rubric__cell {
      width: $mobile-rubric-width;
      height: $mobile-rubric-height;
    }

    .c-rubric__cell-content {
      width: $mobile-rubric-width;
      height: $mobile-rubric-height;
    }

    .c-rubric__remove-cell-button {
      bottom: -5px;
      right: 20px;
    }

    .c-rubric__add-cell-button {
      bottom: -5px;
      right: -5px;
    }

    .c-rubric__remove-row-button {
      bottom: -7px;
    }

    .c-rubric__add-row-button {
      bottom: -7px;
    }

    .md-text-field--margin {
      margin-top: 5px;
    }
  }
}

.rubric-dialog-title {
  display: flex;
  align-items: center;
}

.rubric-dialog-title h2 {
  margin: 0;
  flex: 1;
}

.rubric-dialog button.md-text--theme-primary {
  color: themed-color('button-raised-primary-background');
}

.rubric-dialog button.md-text--theme-secondary {
  color: themed-color('button-raised-secondary-background');
}

.rubric-dialog .c-rubric__selected-row-icon {
  color: themed-color('status-completed');
}
