@import "common/styles/bongo-vars.scss";

.interactive-video-questions {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-top: 16px;
  overflow: auto;
 
  .interactive-video-question-item {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    min-height: 32px;
    padding: 8px 0;
    border-bottom: 1px solid $bongo-color-secondary-gray-light;

    $position-width-min: 24px;
    $position-margin-right: 8px;
    $text-width-min: 128px;
    $text-width-min-small: ($text-width-min - $position-width-min - $position-margin-right);
    .interactive-video-question-item--time {
      display: flex;
      justify-content: center;
      width: 56px;
      min-width: 56px;
      margin-right: 8px;
      padding: 0;
    }
   
    .interactive-video-question-item--type {
      display: flex;
      align-items: center;
      margin-right: 8px;
    }

    .interactive-video-question-item--position {
      min-width: 24px;
      margin-right: 8px;
      display: flex;
      flex-direction: column;

      .interactive-video-question-item--position-up,
      .interactive-video-question-item--position-down {
        width: 24px;
        height: 24px;
        padding: 0;
      }
    }

    .interactive-video-question-item--text {
      flex: 1;
      margin-right: 16px;
      max-height: 100px;
      overflow-y: auto;
      min-width: $text-width-min;
     
      &.interactive-video-question-item--text-with-position {
        min-width: $text-width-min-small;
      }
    }

    .interactive-video-question-item--weight {
      max-width: 48px;
      flex-shrink: 0;
    }
  }
}
