.combine-video-list-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;

  .combine-video-list-item--checkbox {
    display: flex;
    align-items: center;
    min-height: 48px;
    height: auto;
    flex: 1;
  }

  .combine-video-list-item--drag-handle {
    display: flex;
  }
}
