@import "common/styles/mixins.scss";

.instructions-card {
  @include clearfix;
  clear: both;
  display: block;
  min-height: 1px;
  height: auto !important;
  width: auto;
  margin: 0 auto 20px auto;
  position: relative;

  .instructions-description {
    padding: 0 !important;
    background-color: black;
  }
 
  .instructions-title {
    padding: 0;
  }
 
  .instructions-video {
    float: left;
    margin: 0 20px 0 0;
    text-align: center;
    width: 75%;
    max-width: 400px;
  }

  .instructions-detail-container {
    padding: 16px;

    p {
      white-space: pre-wrap;
      overflow-wrap: break-word;
    }
  }

  .card-contents {
    background-color: white;
  }

  .url-instructions {
    height: 400px;
    clear: both;
  }

}

.instructions-card.has-media {
  padding: 0;

  .card-contents {
    padding: 16px;
  }
}

@media all and (max-width: 1000px) {
  .instructions-card {
    .instructions-description, .instructions-title, .instructions-video {
      clear: both;
      display: block;
      float: none;
      margin: 0 auto !important;
      width: auto;
    }
   
    .instructions-description {
      padding: 20px;
    }
   
    .instructions-video {
      margin: 20px;
    }
  }
}

@media all and (max-width: 700px) {
  .instructions-detail-container {
    padding: 16px 10px;
  }
}
