@import 'common/styles/theme.scss';
@import 'common/styles/bongo-vars.scss';

.closeable-dialog-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeable-dialog {
  // .md-dialog, .md-dialog--centered
  // Override .md-dialog--centered styles (centering is achieved via overlay container with flex layout
  position: static;
  left: unset;
  top: unset;
  transform: unset;
  max-height: calc(100vh - 32px);
  max-width: calc(100vw - 32px);

  display: flex;
  flex-direction: column;
  align-items: stretch;
  background: themed-color('surface-background');
  width: auto;

  .closeable-dialog-header {
    display: flex;
    margin: 0;

    .closeable-dialog-header-title {
      display: flex;
      align-items: center;
      flex: 1;
      margin: 0;
    }

    .closeable-dialog-header-actions {
      display: flex;
      flex-direction: row;
      flex: 0;
      margin-left: 8px;
    }
  }

  .closeable-dialog-content {
    // .md-dialog-content
    // This <section> gets a max-height calculated by react-md, padding must be set here and not in parent
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 16px;
  }

  .closeable-dialog-children-container {
    display: flex;
    flex-direction: column;
    padding: 8px 0 0 0; // TODO: use padding instead?
    overflow: auto;
    min-height: 100px;
  }

  .closeable-dialog-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 8px 0 0 0;
    width: 100%;
  }

  .closeable-dialog-toolbar {
    padding: 0 0 0 16px;
  }
}

.closeable-dialog--full {
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  max-height: unset;
  max-width: unset;

  .closeable-dialog-content {
    padding: 0;
    margin: $bongo-toolbar-height 0 0 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    flex: 1;

    .closeable-dialog-children-container {
      margin: 0;
      padding: 8px 8px 0 8px;
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: stretch;
    }

    .closeable-dialog-footer {
      padding: 4px 8px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
  }
}
