.c-recorder {
  .c-recorder__uploading-layout {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 5;
  }

  .c-recorder__uploading-layout-text {
    color: #f1f1f1;
    font-size: 20px;
    font-weight: bold;
    position: absolute;
    z-index: 2;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
    line-height: 30px;
    text-shadow: 1px 0 0 #545454,
    1px 1px 0 #545454,
    0 1px 0 #545454;
  }

  .c-recorder__uploading-layout-link {
    color: #88cc78;
    text-decoration: underline;
    cursor: pointer;
    &:hover {
      text-decoration: none;
    }
  }

  .c-recorder__uploading-layout-progress {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    height: 100%;
    background-color: rgba(69, 251, 69, .15);
    filter: blur(10px);
    transition: all .6s;
  }
}
