@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.chat-notification-dialog, .activity-log-notification-dialog {
    width: 40% !important;
    max-width: 500px;
    min-width: 320px;
}

.chat-notification-dialog .md-dialog-content {
    overflow: hidden;
}

#activity-log-notification-button.md-btn--hover, #chat-notification-button.md-btn--hover {
    background: themed-color('button-raised-primary-contrast', .1);
}

@media all and (max-width: 700px) {
    #activity-log-notification-button.md-btn--hover, #chat-notification-button.md-btn--hover {
        background: none;
    }
}
