@import "common/styles/mixins.scss";

$full-width-breakpoint: 700px;

.c-bbb-player {
  padding: 10px;
  background-color: white;
  width: 100%;

  .c-bbb-player__deskshare-player {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
  }

  .c-bbb-player__deskshare-player--fullscreen {
    height: 100%;
  }

  .c-bbb-player__deskshare-player-controls {
    z-index: 4;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  .c-bbb-player__presentation-wrapper {
    position: relative;
    width: 50%;
    display: table-cell;
    overflow: hidden;

    &:hover .c-player__controls {
      transform: translateY(0);
    }
  }

  .c-bbb-player__presentation-wrapper--fullscreen {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;

    .c-bbb-player__slides svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }



  .c-bbb-player__fullscreen-button {
    position: absolute;
    bottom: -7px;
    right: -5px;
    z-index: 2;
  }

  .c-bbb-player__top {
    display: table;
    width: 100%;
    position: relative;
  }

  .c-bbb-player__player {
    width: 50%;
    display: table-cell;

  }

  .c-bbb-player__cursor {
    width: 10px;
    height: 10px;
    background-color: darkred;
    border-radius: 50%;
    position: absolute;
    transform: translateX(50%) translateY(50%);
  }

  .c-bbb-player__bottom {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    > * {
      min-width: 50%;
      flex: 1;
    }
  }

  @media (max-width: $full-width-breakpoint) {
    .c-bbb-player__top {
      display: flex;
      flex-direction: column;
    }

    .c-bbb-player__player {
      width: 100%;
      display: block;
      order: 1;
    }

    .c-bbb-player__presentation-wrapper {
      display: block;
      width: 100%;
    }
  }
}

.c-bbb-player__error-block {
  padding: 10px;
}
