.instructions-section-form {
    margin: 0 0 24px 0;
}

.instructions-section-form h2 {
    margin: 0;
}

.instructions-container {
    display: flex;

    .c-video-container {
        max-width: 200px;
    }

  .preview-url-button {
    position: relative;
    top: 25px;
  }
}

.instructions-video-container {
    background-color: black;
    margin: 0 20px 0 0;
    max-width: 200px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

@media all and (max-width: 600px) {
  .instructions-container,
  .instructions-container .c-video-container {
      flex-direction: column;
      max-width: 100%;
  }
 
  .instructions-container {
      width: 100%;
  }
 
  .instructions-container .c-video-container {
      margin: 0 auto;
      width: 100%;
  }
}

.instructions-wait-icon {
    color: white;
}

.instructions-delete-media-button {
    color: white;
    position: absolute;
    top: 5px;
    right: 5px;
}

.instructions-video-container button {
    color: white;
}

.instructions-textfield {
    flex: 1;
}

.instructions-container {
    margin-top: 24px;
}

@media all and (max-width: 700px) {
  .instructions-container label, .instructions-container #instructions-textfield,
  .instructions-container #post-submission-instructions-textfield {
      white-space: normal !important;
      max-width: 100%;
  }
 
  .instructions-container #post-submission-instructions-textfield {
      line-height: 2.5;
  }
 
  .instructions-container #instructions-textfield,
  .instructions-container #post-submission-instructions-textfield {
      min-height: 1px !important;
      height: auto !important;
  }
}
