@import 'common/styles/theme.scss';

.bongo-main .grading-student-actions {
  .button {
    @include themed-button('page-toolbar-button');
  }
}

.group-submission-summary-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap-reverse;

  > * {
    margin-right: 16px;
    flex: 0;

    &:last-child {
      margin-right: 0;
    }
  }

  .group-submission-summary-right {
    flex: 1;
    min-width: 250px;
  }
}

.submission-summary.submission-summary--group {
  background-color: #fff;
  border-radius: 6px;
  padding: 4px 8px;

  .submission-summary--item {
    .submission-summary--item-icon {
      font-size: 24px;
    }
  }
}
