@import "common/styles/bongo-vars.scss";

.interactive-video-settings-container {
  flex: 1;

  /*
  .interactive-video-settings--validation-error {
    border-bottom: 2px solid $bongo-color-avatars-red;
    margin-bottom: -2px;
  }
  */

  .interactive-video-settings {
    padding: 0 16px;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;

    .interactive-video-settings-top-container {
      flex: 1;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;

      .interactive-video-settings-top-left-container {
        flex: 1;
        min-width: 200px;

        .interactive-video-settings-media {
          margin: 0;
        }
      }

      .interactive-video-settings-top-right-container {
        flex: 1;
        padding: 16px;
        min-width: 200px;
      }

      .interactive-video-settings-counter {
        display: flex;
        flex: 1;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: 8px;

        h4 {
          display: inline-block;
          margin: 0 8px 0 0;
        }
      }

      .interactive-video-settings-message {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: center;
        margin-bottom: 8px;

        .interactive-video-settings-progress {
          height: 6px;
        }
      }

      .interactive-video-settings-actions {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: center;

        .interactive-video-settings-actions-create {
          height: auto;
        }
      }
    }

    .interactive-video-settings-config {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      flex: 1;
      flex-wrap: wrap;
      width: 100%;
      max-width: 600px;
      margin-top: 8px;

      .interactive-video-settings-grade-type-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        padding: 0;

        .interactive-video-settings-grade-type-dropdown {
          min-width: 200px;
          padding-right: 8px;
          flex: 1;
        }
       
        .interactive-video-settings-grade-type-rubric {
          min-width: 128px;
          padding-right: 40px; /* relative positioned 3-dot menu */
          flex: 1;

          .md-text > .md-text-field > .md-icon-text {
            flex: 1;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
    }

    .interactive-video-settings-questions-container {
      flex: 1;
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: 600px;
      /*border: 1px solid $bongo-color-secondary-gray-light;*/

      .interactive-video-settings-summary {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        padding: 0 16px;
        margin-top: 16px;

        .interactive-video-settings-summary-list {
          display: flex;
          flex-direction: column;
          align-items: flex-end;
        }

        .interactive-video-settings-summary-item {
          padding: 4px 0;
          flex: 1;
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: flex-end;
        }

        .interactive-video-settings-summary-item-separator {
          width: 100%;
          height: 1px;
          background-color: $bongo-color-secondary-gray-light;
        }

        .interactive-video-settings-summary-label {
          display: inline-block;
          text-align: right;
          margin: 0;
        }

        .interactive-video-settings-summary-value {
          display: inline-block;
          width: 64px;
          text-align: center;
          margin: 0;
        }
      }
    }

    .interactive-video-settings-validation-container {
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      margin-top: 16px;
      width: 100%;
      max-width: 600px;

      .interactive-video-settings-validation-icon {
        color: $bongo-color-avatars-red;
        font-size: 24px;
      }

      .interactive-video-settings-validation-errors {
        flex: 1;
        display: flex;
        flex-direction: column;

        h5 {
          flex: 1;
          color: $bongo-color-avatars-red;
          margin: 0 0 0 8px;
        }
      }
    }
  }
}

.interactive-video-settings-restricted-summary-container {
  margin-top: 24px;
  display: flex;
  align-items: center;

  h2 {
    margin: 0;
    flex: 1;
  }
}

[style*="--aspect-ratio"] > :first-child {
  width: 100%;
}

[style*="--aspect-ratio"] > img {
  height: auto;
}

@supports (--custom: property) {
  [style*="--aspect-ratio"] {
    position: relative;
  }

  [style*="--aspect-ratio"]::before {
    --aspect-ratio: inherit;
    content: "";
    display: flex;
    padding-bottom: calc(100% / (var(--aspect-ratio)));
  }

  [style*="--aspect-ratio"] > :first-child {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
  }
}
