@import 'common/styles/theme.scss';

.questions-container {
  margin: 0 0 24px 0;

  .c-qa-question .c-video-container {
    max-width: 200px;
  }
}

@media all and (max-width: 700px) {
  .questions-container,
  .questions-container .c-qa-question,
  .questions-container .c-qa-question .c-video-container {
    display: block;
  }

  .questions-container, .questions-container .c-qa-question {
    width: 100%;
  }

  .questions-container .c-qa-question .c-video-container {
    margin: 0 auto;
    height: 100px;
    max-width: 100%;
  }

  .questions-container .c-qa-question label, .questions-container .c-qa-question input,
  .questions-container .c-qa-question input {
    white-space: normal !important;
  }

  .questions-container .c-qa-question input {
    line-height: 2.5;
    min-height: 1px !important;
    height: auto !important;
  }

  .questions-container .c-qa-question .md-selection-control-toggle, .questions-container .c-qa-question span {
    display: block;
  }

  .questions-container .c-qa-question .md-ink-container {
    margin: 0 !important;
  }
}

.add-question-button {
  margin: 0 0 0 15px;
}

@media all and (max-width: 700px) {
  .add-question-button {
    min-height: 1px;
    height: auto !important;
    line-height: normal;
    margin: 0;
  }
}

.record-time-container {
  display: flex;
  flex-direction: row;
  > div {
    flex: 1;
  }
}

.max-record-time-input {
  margin-left: 16px;
}

@media all and (max-width: 900px) {
  .record-time-container {
    flex-direction: column;
  }

  .max-record-time-input {
    margin-left: 0;
  }
}

.qa-questions-list-item-drag-container--active {
  background-color: #fff;
  outline-width: 2px;
  outline-offset: -2px; // Not supported on IE11
  outline-style: dashed;
  outline-color: themed-color('button-raised-primary-background');
}

.qa-assignment-settings-recording-mode-container {
  margin-bottom: 12px;

  .qa-assignment-settings-recording-mode-header {
    margin: 0;
  }

  .radio-recording-mode-option-text {
    display: flex;
    align-items: center;
  
    .font-icon {
      margin-left: 4px;
    }
  }
}


.qa-answer-settings-contents {
  display: flex;
  flex-direction: column;
}
