@import 'common/styles/theme.scss';

.c-player__prev-button {
  display: table-cell;
  width: 25px;
  text-align: center;
  position: relative;
}

.c-player__prev-button-icon {
  font-size: 20px;
  width: 100%;
  color: themed-color('page-contrast') !important;
  line-height: 25px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);

  &:hover {
    color: rgb(0, 188, 212);
  }
}

.c-player__prev-button-icon.c-player__prev-button-icon--disabled {
  color: #8e8e8e !important;
  cursor: default;
  &:hover {
    color: #8e8e8e !important;
  }
}
