@import 'common/styles/theme.scss';

.input-switch {
  // switch thumb should never have any transparency applied
  // need to indicate selected state even when disabled too
  .md-switch-thumb--on {
    background: themed-color('selection-control-selected');
    &.md-switch-thumb--disabled {
      background: themed-color('selection-control-selected');
    }
  }
  // switch track can have transparency applied (also to indicate disabled)
  .md-switch-track--on {
    background: themed-color('selection-control-selected', .5);
    &.md-switch-track--disabled {
      background: themed-color('selection-control-unselected', .5);
    }
  }

  .md-switch-thumb--off {
    background: white;
    &.md-switch-thumb--disabled {
      background: themed-color('selection-control-unselected');
    }
  }
  .md-switch-track--off {
    background: themed-color('selection-control-unselected');
    &.md-switch-track--disabled {
      background: themed-color('selection-control-unselected', .5);
    }
  }

  .md-fake-btn {
    @include themed-ink('selection-control-selected', 'md-ink-container');
  }
}
