@import "common/styles/bongo-vars.scss";

.interactive-video-summary-card-body {
  padding: 0;
}

.interactive-video-summary-answers-container {
  padding: 16px;
  display: flex;
  flex-direction: column;
}

.interactive-video-summary-empty-answers {
  font-style: italic;
  padding: 16px;
}

.interactive-video-summary-answer-item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 8px;

  .interactive-video-summary-answer-item-label-value-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .interactive-video-summary-answer-item-label {
    flex-shrink: 0;
    margin-right: 4px;
  }

  .interactive-video-summary-answer-item-right-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .interactive-video-summary-answer-item-index {
    margin-right: 8px;
    width: 24px;
    display: flex;
    justify-content: flex-end;
  }

  .interactive-video-summary-answer-item-correct {
    display: flex;
    margin-top: 4px;
  }

  .interactive-video-summary-answer-item-type {
    display: flex;
    margin-right: 8px;

    .interactive-video-summary-answer-item-icon {}
  }

  .interactive-video-summary-answer-item-middle-container {
    display: flex;
    flex-direction: column;
    flex: 1;

    .interactive-video-summary-answer-item-question {
      font-weight: bold;
    }

    .interactive-video-summary-answer-item-answer {
      margin-top: 8px;
      margin-left: 16px;
    }

    .interactive-video-summary-answer-item-feedback {
      margin-left: 8px;

      &.interactive-video-summary-answer-item-feedback--correct {
        color: $bongo-color-avatars-green;
      }

      &.interactive-video-summary-answer-item-feedback--incorrect {
        color: $bongo-color-avatars-red;
      }
    }
  }

  .interactive-video-summary-answer-item-weight {
    width: 48px;
    display: flex;
    justify-content: center;
  }
}
