.c-recorder {

  .c-recorder__notifications {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 6;
  }

  .c-recorder__fix-button {
    display: inline-block;
    border: none;
    background-color: white;
    border-radius: 3px;
    padding: 0 3px;
    margin: 0 0 0 10px;
    cursor: pointer;
  }

  .c-recorder__notification {
    width: 100%;
    padding: 5px;
    display: block;
    p {
      color: white;
      font-weight: bold;
      text-align: center;
      margin: 0;
    }
  }
  .c-recorder__notification--error {
    background-color: #ff7d7d;
    border-bottom: 2px solid #cf6868;
  }

  .c-recorder__notification--warn {
    background-color: #f7bc4f;
    border-bottom: 2px solid #b98d3b;
  }

  .c-recorder__notification--info {
    background-color: #96d0ff;
    border-bottom: 2px solid #729abb;
  }

}