@import "common/styles/bongo-vars.scss";

.progress-linear {
  height: 4px;
}

.stepped-progress-container {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  height: 20px;
  width: 100%;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  background-color: $bongo-color-secondary-gray-light;

  .stepped-progress-complete {
    position: absolute;
    left: 0;
    background-color: $bongo-color-blue;
    height: 100%;
    transition-timing-function: ease;
    transition-duration: 1.0s;
  }

  .stepped-progress-label {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-weight: bold;
    font-size: 12px;
    color: white;
  }

  .stepped-progress-interval {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;

    .stepped-progress-interval-item {
      position: absolute;
      width: 1px;
      height: 100%;
      background-color: rgba(255, 255, 255, .75);
    }
  }
}
