@import 'common/styles/theme.scss';

.c-video-container {
  background-color: #000;
  margin: 0 16px 0 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .button.c-video-container__add-button {
    $color: #fff;
    $color-disabled: #ccc;
    @include create-button-skin(
      transparent,
      $color,
      transparent,
      $color-disabled
    );
    @include create-ink-skins($color);

    height: 64px;
    width: 64px;

    i {
      font-size: 32px;
    }
  }

  .c-video-container__wait-icon {
    color: white;
  }

  .c-player__controls {
    padding-right: 40px;
  }

  .dialog-overlay {
    .c-player__controls {
      padding-right: 0;
    }
  }

  .button.c-video-container__toolbar-button {
    $color: #fff;
    $color-disabled: #ccc;
    @include create-button-skin(
      transparent,
      $color,
      transparent,
      $color-disabled
    );
    @include create-ink-skins($color);

    border-radius: 0;
  }

  .deskshare-container {
    position: absolute;
    top: 0;
    left: 0;
    padding: 6px;
    background: rgba(0, 0, 0, .4);

    .c-video-container__deskshare-button {
      color:#fff;
      font-size: 32px;
    }
  }

  @mixin focusedActions {
    visibility: visible;
    opacity: 1;
    width: 40px;
    transition: opacity .5s ease;
    transition: width .5s ease;
  }

  .video-actions {
    display: flex;
    visibility: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    width: 0;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background: #000;
    opacity: 0;
    overflow: hidden;

    &.focused {
      @include focusedActions;
    }
  }

  &:hover, &:focus-within {
    .video-actions {
      @include focusedActions;
    }
  }
}
