// SONAR.IGNOREFILE
@function bongo-var-name($name, $scope: 'default') {
  @return --bongo-#{$name}__#{$scope};
}

@function themed-style($name, $default: 0) {
  $default-var-name: bongo-var-name($name);
  $custom-var-name: bongo-var-name($name, 'custom');
  $default-val: var($default-var-name, $default);
  @return var($custom-var-name, $default-val);
}

@function themed-color($name, $opacity: null) {
  $color-name: '#{$name}-color';
  $color-vals: themed-style($color-name);

  @if ($opacity) {
    @return rgba($color-vals, $opacity);
  }

  $opacity-name: '#{$color-name}--opacity';
  $opacity-val: themed-style($opacity-name, 1);

  @return rgba($color-vals, $opacity-val);
}

@function default-color($name, $opacity: null) {
  $color-name: '#{$name}-color';
  $color-var-name: bongo-var-name($color-name);
  $color-val: var($color-var-name, 0);

  @if ($opacity) {
    @return rgba($color-vals, $opacity);
  }

  $opacity-name: '#{$color-name}--opacity';
  $opacity-var-name: bongo-var-name($opacity-name);
  $opacity-val: var($opacity-var-name, 1);

  @return rgba($color-val, $opacity-val);
}

// The opacity value of the overlay should be adjusted for each different combination of text
// on surface in order to ensure proper visibility and color contrast accessibility.
// [hover]  Hover overlays should be at lower opacities to avoid distracting from content.
//          eg. 4% if dark overlay on light background (8% if light on dark)
// [focus]  Focus overlays should be at higher opacities as they are not indicated otherwise by visual cues.
//          eg. 12% if dark overlay on light background (24% if light on dark)
// [active] Pressed overlays should be at higher opacities since they trigger a change in layout.
//          eg. 16% if dark overlay on light background (32% if light on dark)

$hover-opacity-default: .06;
$focus-opacity-default: .12;
$active-opacity-default: .18;
// TODO: Figure out how we can support having a border on focus
// $focus-border-opacity-default: $focus-opacity-default * 2;

@mixin themed-ink($color-name, $ink-overlay-classname) {
  $color: themed-color($color-name);
  @include create-ink-skins($color, $ink-overlay-classname);
}

@mixin create-ink-skins(
  $color,
  $ink-overlay-classname: 'button-ink-overlay',
  $hover-opacity: $hover-opacity-default,
  $focus-opacity: $focus-opacity-default,
  $active-opacity: $active-opacity-default
) {
  &:hover .#{$ink-overlay-classname} {
    @include create-ink-skin($color, $hover-opacity);
  }
  &:focus .#{$ink-overlay-classname} {
    @include create-ink-skin($color, $focus-opacity);
  }
  &:active .#{$ink-overlay-classname} {
    @include create-ink-skin($color, $active-opacity);
  }

  // Hide all button-ink-overlay children
  // (since we define the hover/focus/active(pressed) states on the button-ink-overlay and md-ink animations are disabled)
  .#{$ink-overlay-classname} * {
    display: none;
  }
}

@mixin create-ink-skin($color, $opacity) {
  background: $color;
  opacity: $opacity;
}

@mixin themed-button($type, $skin: null, $ink-overlay-classname: 'button-ink-overlay') {
  $type-skin-name: $type;
  @if ($skin) {
    $type-skin-name: '#{$type}-#{$skin}';
  }

  $background-name: '#{$type-skin-name}-background';
  $contrast-name: '#{$type-skin-name}-contrast';
  $disabled-background-name: '#{$type}-disabled-background';
  $disabled-contrast-name: '#{$type}-disabled-contrast';

  @include themed-ink($contrast-name, $ink-overlay-classname);
  @include create-button-skin(themed-color($background-name), themed-color($contrast-name), themed-color($disabled-background-name), themed-color($disabled-contrast-name));
}

@mixin create-button-skin($background-color, $contrast-color, $disabled-background-color, $disabled-contrast-color) {
  background: $background-color;
  color: $contrast-color;
  &[disabled] {
    background: $disabled-background-color;
    color: $disabled-contrast-color;
  }
  .font-icon {
    color: inherit;
  }
  .md-circular-progress-path {
    stroke: $contrast-color;
  }
}
