.feedback-rating-container {
  margin: 0 auto;
  padding-bottom: 6px;
}

.feedback-card-header {
  align-items: center;
  flex-wrap: wrap;
}

.feedback-card-header h2 {
  margin: 0;
  flex: 1;
}

.feedback-card-header .feedback-rating-container {
  margin: 0;
}

.self-assessment-rubric-results {
  justify-content: center;
  display: flex;
  align-items: center;
}

.self-assessment-rubric-results span {
  font-size: 16px;
}

.self-assessment-rubric-results button {
  min-width: 66px;
}

.peer-review-fivestar-container {
  display: flex;
  align-items: center;
}

.peer-review-fivestar-container span {
  margin-left: 8px;
  font-size: 14px;
}
