@import "common/styles/mixins.scss";
@import 'common/styles/bongo-vars.scss';

.dashboard-container {
    display: flex;
    flex-direction: row;
    padding: 0 0 16px 0;
}

.dashboard-card {
    margin: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 0;
    /*height: 164px;*/
    /*max-height: 200px;*/
    overflow: hidden;
    /* padding: 8px 16px; */
    position: relative;
    margin-right: 16px;
}

.dashboard-card {
    min-height: 120px;
}

.dashboard-container .dashboard-card:last-child {
    margin-right: 0 !important;
}

@media all and (max-width: 1024px) {
    .dashboard-container .dashboard-card:nth-child(2) {
        margin-right: 0;
    }

    .dashboard-container .dashboard-card:nth-child(3), .dashboard-container .dashboard-card:nth-child(4) {
        display: none;
    }
}

@media all and (max-width: 800px) {
    .dashboard-container {
        flex-direction: column;
    }

    .dashboard-card {
        margin: 0;
        flex: 0;
    }

    .dashboard-card:not(:first-child) {
        margin-top: 16px;
    }
}

.dashboard-container .avatar {
    margin: 0 4px 0 0 !important;
}

.dashboard-card-icon {
    /*color: #d8def3;*/
    color: $bongo-color-secondary-gray-light;
    opacity: .3;
    font-size: 160px !important;
    position: absolute;
    right: 4px;
    bottom: -50px;
}

.dashboard-title {
    position: relative;
    z-index: 13;
}

.dashboard-contents {
    padding: 0;
}

.dashboard-card-title {
    padding: 0;
}

.dashboard-card-title h2 {
    margin: 0;
}

/*
.dashboard-card-title h2 {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 2px;
}
*/

.dashboard-card {
    .dashboard-card-body {
        padding: 0;
        display: flex;
        flex-grow: 1;
    }
}

.dashboard-card-actions {
    padding: 0;
}

.dashboard-card-contents {
    align-self: flex-end;
}

.dashboard-card-with-actions .dashboard-card-contents {
    align-self: flex-start;
}

.assignment-submitted-count {
    margin: 0 8px 0 0;
}

.assignment-submitted-font {
    font-size: 32px;
}

.assignment-submitted-total {
    margin: 0 0 0 8px;
}

.centered {
    float: none !important;
    margin: 0 auto !important;
    text-align: center !important;
}

.icon-avatar-info, .dashboard-contents {
    color: rgba(0, 0, 0, .87);
    font-size: 32px !important;
    margin: 0 !important;
    position: static;
}

.icon-avatar-info {
    color: rgba(0, 0, 0, .87);
    line-height: 70px !important;
    padding: 0;
    position: static;
    white-space: nowrap;
    overflow: hidden;
}

.eval-nav-container {
    display: flex;
    height: 100%;
    flex-direction: row;
    padding: 4px 0;
    flex: 1;
}

.eval-nav-contents {
    flex-grow: 1;
    margin-left: -10px;    // need this because the buttons in the card take too much space. This makes it look better
    margin-right: -10px;
    width: calc(100% - 96px);
}

.evals-card {
    padding: 0;
}

.eval-left-icon, .eval-right-icon {
    align-self: center;  
}

.icon-avatar-info, .icon-avatar-info * {
    vertical-align: top;
}

.margin-centered {
    float: none !important;
    margin: 0 auto !important;
}

.submitted-by-user-container {
    display: flex;
    flex-direction: row;
}

.submitted-by-avatar-container {
    display: flex;
    height: 60px;
    align-items: center;
}

.submitted-by-details-container {
    align-items: center;
    flex: 1;
    font-size: 24px;
    padding: 0 0 0 6px;
    min-height: 60px !important;
    height: auto !important;
    height: 60px;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.submitted-by-details-date {
    font-size: 14px;
}

.submitted-by-card-body {
    flex-grow: 0;
}
