@import "common/styles/mixins.scss";

.c-grade-table {
  padding: 0 24px;

  .c-grade-table__default-cell {
    width: 20%;

    .c-grade-table__fake-button {
      &:focus {
        text-decoration: underline;
      }
 
      a {
        @include noselect;
      }
    }
  }

  .c-grade-table__peer-review-fivestar {
    display: flex;
    align-items: center;
  }

  .c-grade-table__peer-review-fivestar-count {
    margin: 0 0 0 5px;
    position: relative;
    top: 1px;
  }

  .c-grade-table__is-draft{
    margin: 0;
  }

}
