.c-rubric-evaluations-table {
  width: 100%;
  text-align: center;
  border-collapse: collapse;

  td, th {
    padding: 5px;
    border: 1px solid #e3e3e3;
  }

  .c-rubric-evaluations-table__evaluation-row {
    cursor: pointer;

    &:hover {
      background-color: #d6f9f7;
    }
  }

  .c-rubric-evaluations-table__average-cell {
  }
}

.c-rubric-evaluation-dialog {
  max-width: 800px;
  width: 95%;
}