

.bubble-ele-container { 
	position: absolute;
	bottom: 0;
    opacity: 0;
    animation-name: bubblerise;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in;
}

.bubble-ele { 
	width: 6px;
	height: 6px;
	margin: 0 auto;
	border: 1px solid rgba(227, 227, 236, .9);
	background: rgba(255, 255, 255, .85);
    border-radius: 20px;
    animation-name: bubblewobble;
    animation-duration: .5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}


@keyframes bubblerise {
	0% {   
		bottom: 0;
        opacity: 0;
	}
	5% {   
		bottom: 0;
        opacity: 1;
	}
	99% {
		opacity: 1;
	}
	100% {   
		bottom: 100%;
        opacity: 0;
	}
}


@keyframes bubblewobble {
	0% {
		margin-left: 0;
	}
	50% {
		margin-left: 4px;
	}
}
