.group-formation-filter-container {
    display: flex;
    align-items: center;
    position: relative;
    margin: 12px 0 18px 0;
    background-color: white;
    padding: 8px;
}

.group-formation-filter-icon {
    padding: 8px;
}

.group-formation-filter-container .input-text hr {
    opacity: 0;
}

.group-formation-group-container {
    padding: 16px;
    display: flex;
    align-items: center;
}

.group-formation-group-container:hover {
    background-color: #eee;
}

.group-formation-group-name {
    flex: 1;
}

.group-formation-group-status {
    padding-right: 16px;
}

.group-list-container {
    display: flex;
}

.group-list {
    flex-basis: 35%;
    min-height: 400px;
    padding-right: 16px;
}

.group-details-container {
    display: flex;
    flex-direction: column;
    flex: 1;
    border-left: 1px solid #ddd;
    padding-left: 16px;
}

.group-details-header {
    display: flex;
}

.group-details-header-title {
    flex: 1;
}

.group-details-header h2 {
    margin: 0;
}

.group-members-empty-container {
    padding: 16px;
}

.group-members-empty-container p {
    font-style: italic;
    font-size: 14px;
}

.group-details-body {
    flex: 1;
}

.group-details-body.empty-members {
    flex: 1;
    justify-content: center;
    display: flex;
    align-items: center;
}
