    url('bongo.ttf?1jvdd0') format('truetype'),
    url('bongo.woff?1jvdd0') format('woff'),
    url('bongo.svg?1jvdd0#bongo') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon {
