@import 'common/styles/theme.scss';

.star-rating {
  font-size: 32px;
}

.dv-star-rating .dv-star-rating-empty-star {
  color: #fff !important;
  margin: 0 5px;
  text-stroke: 2px #000;
  -webkit-text-stroke: 2px #000;
}

.dv-star-rating .dv-star-rating-full-star {
  color: themed-color('button-raised-secondary-background') !important;
  margin: 0 5px;
  text-stroke: 2px themed-color('button-raised-secondary-background');
  -webkit-text-stroke: 2px themed-color('button-raised-secondary-background');
}

.rsr-root {
  vertical-align: middle;
  display: inline-block;
}

.rsr--editing:hover {
  cursor: pointer;
}

.rsr {
  position: relative;
  vertical-align: middle;
  display: inline-block;
  color: #e3e3e3;
  overflow: hidden;
}

.rsr__stars {
  position: absolute;
  left: 0;
  top: 0;
  white-space: nowrap;
  overflow: hidden;
  color: #f5a71b;
  transition: all .01s;
}

.rsr__caption {
  font-size: 1.25em;
  vertical-align: middle;
  margin-right: .5em;
}

.rsr--disabled .rsr-container:hover {
  cursor: not-allowed;
}

.input-rating {
  display: flex;
  align-items: center;
  align-self: flex-end;
}

.input-rating .dv-star-rating {
  display: flex !important;
  flex-direction: row-reverse;
}

.rating-summary-dialog {
  width: auto;
}

.rating-comment-column {
  white-space: normal;
}

.rating-summary-button {
  min-width: 66px;
  margin-left: 4px;
}

.rating-result-item {
  padding: 16px 0 16px 0;
}
.rating-results-container .rating-result-item:not(:first-child) {
  border-top: 1px solid #ddd;
}

.rating-comments-container {
  white-space: normal;
  font-size: 14px;
  margin-top: 8px;
}

@media all and (max-width: 700px) {
  .rating-summary-dialog {
    width: 100%;
  }
}

@media all and (max-width: 500px) {
  .member-avatar {
    display: none;
  }
}
