.activity-toolset-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .activity-toolset-primary {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 0 24px 0;

    > div {
      margin-right: 12px;
    }

    > div:last-child {
      margin-right: 0;
    }
  }

  .activity-toolset-secondary {
    display: flex;
    margin: 0;
  }
}
