.c-recorder {
  .c-recorder__stop-button-holder {
    display: flex;
  }

  .c-recorder__stop-button-tooltip {
    width: 100%;
    height: 100%;
  }

  .c-recorder__stop-button {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    background-color: #ff5962;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: bold;
    transition: all .3s;

    &:hover,
    &:focus {
      background-color: #e0535c;
    }
  }

  .c-recorder__stop-button.c-recorder__stop-button--disabled {
    background-color: #e3dcdd;
    color: #8a8787;
  }
}
