@import 'common/styles/theme.scss';

body {
  background-color: themed-color('page-background');
  height: 100%;
  font-size: 12px;
  color: #555;
}

body * {
  box-sizing: border-box;
}

a {
  color: #006fbf;
  cursor: pointer;
}

p {
  color: rgba(0, 0, 0, .87);
}

input,
textarea,
select {
  max-width: 100%;
}

.bongo-main h2 {
  font-size: themed-style('surface-h2-font-size');
  font-weight: themed-style('surface-h2-font-weight');
  line-height: themed-style('surface-h2-line-height');
  color: themed-color('surface-h2');
}

.bongo-main h3 {
  font-size: themed-style('surface-h3-font-size');
  font-weight: themed-style('surface-h3-font-weight');
  line-height: themed-style('surface-h3-line-height');
  color: themed-color('surface-h3');
}

.u-clearfix {
  @include clearfix;
}

.u-display-flex {
  display: flex;
}

.u-align-items-center {
  align-items: center;
}

.u-text-center {
  text-align: center !important;
}

.u-justify-end {
  justify-content: flex-end;
}

.u-text-no-break {
  white-space: nowrap;
}

.u-mr-5px {
  margin-right: 5px;
}

.u-pt-10px {
  padding-top: 10px;
}

.u-pt-20px {
  padding-top: 20px;
}

.u-pb-5px {
  padding-bottom: 5px;
}

.u-pb-20px {
  padding-bottom: 20px;
}

.u-pl-10px {
  padding-left: 10px;
}

.u-pl-15px {
  padding-left: 15px;
}

.u-pb-40px {
  padding-bottom: 40px;
}

.u-transparent-50 {
  opacity: .5;
}

.u-m-0 {
  margin: 0;
}

.u-mb-5px {
  margin-bottom: 5px;
}

.u-mb-10px {
  margin-bottom: 10px;
}

.u-mb-25px {
  margin-bottom: 25px;
}

.u-mt-10px {
  margin-top: 10px;
}

.u-fs-14px {
  font-size: 14px;
}

.u-fs-15px {
  font-size: 15px;
}

.u-minh-100px {
  min-height: 100px;
}

.u-minh-200px {
  min-height: 200px;
}

.u-full-width {
  width: 100%;
}

.u-508-only {
  font-size: 0;
  line-height: 0;
  text-indent: -90000px;
  position: absolute;
  left: -900000px;
}

.u-error,
.u-error > p {
  color: $bongo-color-avatars-red;
  font-size: 14px;
}

.u-absolute-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.u-width-50px {
  width: 50px;
}

.u-break-long-words {
  word-break: break-all;
  white-space: pre-wrap;
}
