.c-crate-activity {
  padding: 16px 16px 0 16px;

  .c-crate-activity__block {
    padding: 0;
  }
}

@media all and (max-width: 500px) {
  .c-crate-activity {
    padding: 0;
  }
}

.config-section-contents {
  padding: 0 16px;
}

.settings-form-actions {
  padding: 16px 0;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin-top: 32px;
}

.settings-form-save-button {
  margin-right: 8px;
}

.due-date-container, .due-date-container-readonly {
  display: flex;
}

.due-date-container-readonly button {
  align-self: flex-end;
}

.due-date-container .input-date-picker, .due-date-container .input-time-picker, .due-date-container-readonly .due-date-textfield {
  flex: 1;
}

.due-date-container .input-date-picker {
  padding-right: 8px;
}

.due-date-container .input-time-picker {
  padding-left: 8px;
}

@media all and (max-width: 900px) {
  .due-date-container, .grade-type-container {
      flex-direction: column;
  }

  .due-date-container .input-date-picker {
      padding-right: 0;
  }

  .due-date-container .input-time-picker {
      padding-left: 0;
  }
}

.settings-form-advanced-container {
  margin: 32px 0 0 0;

  > div {
    margin: 16px 0 0 0;
  }

  > div:first-child {
    margin: 0;
  }
}
