$analysis-rating-color-unknown: #485054;
$analysis-rating-color-a: #6eac52;
$analysis-rating-color-b: #e87511;
$analysis-rating-color-c: #d5122c;

.auto-analysis-dialog {
  height: auto;
  width: 85%;
  max-width: 1000px;

  *:focus {
    outline: none;
  }
}

.auto-analysis-dialog-content-container {
  // max-width: 100%;
}

.auto-analysis-dialog-content-loading {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
}

.auto-analysis-dialog-content-waiting {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex: 1;

  .auto-analysis-dialog-content-waiting-left {
    display: flex;
    flex-direction: row;
  }

  .auto-analysis-dialog-content-waiting-right {
    display: flex;
    flex-direction: column;
    margin-left: 8px;
    max-width: 500px;
  }

  .auto-analysis-dialog-content-waiting-spinner {
    font-size: 48px;
    width: 48px;
    height: 48px;
    top: 24px;
    left: 24px;
  }

  .auto-analysis-dialog-content-waiting-title {
    font-size: 18px;
    font-weight: bold;
    height: 48px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
  }

  .auto-analysis-dialog-content-waiting-details {
    font-size: 14px;
  }
}

.auto-analysis-dialog-content-error {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.auto-analysis-dialog-content-report {
  display: flex;
}

.closeable-dialog--full {
  &.auto-analysis-dialog {
    width: 100vw;
    max-width: unset;
  }
}
