.c-gradebook-page {
  .c-gradebook-page__group-card {
    .gradebook-card-top-container {
      display: flex;
      flex-direction: row;
      justify-content: center;
      margin-bottom: 16px;

      .gradebook-search-container {
        max-width: 250px;
        flex: 1;
        border-radius: 20px;
        padding: 0 16px;
      }

      .gradebook-search-input {
        max-width: 250px;

        .gradebook-search-input-clear-button {
          width: 20px;
          height: 20px;
          padding: 0;
          top: unset;
          align-self: center;
        }

        .md-text-field-divider-container {
          margin-left: 4px;
        }

        .md-text-field-icon--positioned {
          align-self: center;
          margin-bottom: 0;
        }

        hr {
          visibility: hidden;
          margin-bottom: 4px;
        }
      }
    }

    .no-search-results-container {
      display: flex;
      flex-direction: column;
      align-items: center;

      h4 {
        margin: 0;
      }
    }

    .group-container {
      margin-bottom: 32px;

      &:last-child {
        margin-bottom: 16px;
      }
    }
  }

  .c-gradebook-page__group-name {
    margin: 0;
  }
}

.gradebook-card {
  display: flex;
  flex-direction: column;

  &.groups-not-formed {
    padding: 16px;
  }

  .info-message {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    font-size: 16px;
    margin-top: 8px;

    span {
      margin-left: 8px;
    }

    i {
      font-size: 24px;
    }
  }
}

.gradebook-error {
  padding: 16px;
}

.gradebook-not-ready {
  display: flex;
  flex-direction: column;

  span {
    padding: 4px;
  }
}
