$answer-option-row-height: 48px;

.interactive-video-answer-option {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;

  .interactive-video-answer-option-radio {}

  .interactive-video-answer-option-text {
    margin-right: 16px;
    height: $answer-option-row-height;
  }

  .interactive-video-answer-option-correct-icon {
    margin-right: 8px;

    &.interactive-video-answer-option-correct-icon--hidden {
      visibility: hidden;
    }
  }

  .interactive-video-answer-option-feedback {
    height: $answer-option-row-height;
  }
}
