@import 'common/styles/bongo-vars.scss';

.activity-grade-card-header {
    display: flex;
    align-items: center;
}

.activity-grade-card-header h2 {
    margin: 0;
    flex: 1;
}

.grade-card-contents {
    width: 100%;
}

.info-contents-label {
    padding-right: 4px;
}

.info-content-section {
    padding: 4px 0 4px 0;
}

.student-dashboard-card-contents {
    display: flex;
    padding-bottom: 8px;
}

.student-details-container {
    flex: 1 1;
    padding: 0 0 0 6px;
    min-height: 60px !important;
    height: auto !important;
    height: 60px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .student-details-name {
        font-size: 24px;
    }
}

.submission-summary {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 4px;

    &:focus {
        outline: none;
    }

    > * {
        margin-left: 12px;
        &:first-child {
            margin-left: 0;
        }
    }

    .submission-summary--item {
        display: flex;
        flex-direction: column;
        align-items: center;

        .submission-summary--item-icon {
            font-size: 32px;
            color: rgba(0, 0, 0, .5);
        }

        .submission-summary--item-count {
            margin-top: 2px;
            font-size: 12px;
            color: white;
            background-color: $bongo-color-blue;
            font-weight: bold;
            text-align: center;
            width: 100%;
            border-radius: 8px;
        }

        &:last-child .submission-summary--item-count {
            background-color: rgba(0, 0, 0, .5);
        }
    }


    .submission-summary--item-separator {
        width: 2px;
        background-color: rgba(0, 0, 0, .5);
        align-self: stretch
    }
}

.submission-summary-tooltip {
    display: flex;
    flex-direction: row;
    padding: 8px 0;

    .submission-summary-tooltip-column {
        display: flex;
        flex-direction: column;
        padding: 0 0 0 12px;
        border-left: 2px solid rgba(255, 255, 255, .2);

        &:first-child {
            padding: 0 12px 0 0;
            border-left: 0;
        }

        .submission-summary-tooltip-column--header {
            font-size: 14px;
            font-weight: bold;
            text-transform: uppercase;
            text-align: left;

        }
        .submission-summary-tooltip-column--content {
            display: flex;
            flex-direction: column;
            text-align: left;
            padding: 4px 0;
        }
    }
}

.student-self-assessment-card-contents {
    font-size: 18px;
    margin-bottom: 10px;
}

.student-self-assessment-rubric-button {
    font-size: 18px;
}

.grading-dashboard-container {
    display: flex;
    flex-direction: row;
    padding: 0 0 16px 0;
}

.grading-dashboard-container .dashboard-card {
    z-index: auto;
}

.grading-dashboard-container .dashboard-card:last-child {
    margin-right: 0 !important;
}

@media all and (max-width: 800px) {
    .grading-dashboard-container {
        flex-direction: column;
    }
}

.grading-dashboard-container .avatar {
    margin: 0 4px 0 0 !important;
}

.submitted-details-date {
    font-size: 14px;
}
