@import 'common/styles/theme.scss';
@import 'common/styles/bongo-vars.scss';

.c-player__full-screen-button {
  display: table-cell;
  width: 30px;
  position: relative;
  vertical-align: middle;
}

.c-player__full-screen-icon {
  color: themed-color('page-contrast') !important;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);

  &:hover {
    color: rgb(0, 188, 212) !important;
  }
}

.rc-slider-custom-dot {
  background-color: $bongo-color-player-slider-aaa-on-light-gray !important;
}
