@import "common/styles/bongo-vars.scss";
@import 'common/styles/theme.scss';

.review-user-selection h2 {
  flex: 0;
}

.peer-review-comment-text {
  display: inline-block;
  align-self: center;
  margin: 0;
}

.message-send-button, .review-add-video-button, .review-edit-comment-button, .review-delete-comment-button {
  color: themed-color('page-contrast');
}

.message-send-button:disabled .md-icon, .message-send-button:disabled:hover .md-icon,
.review-add-video-button:disabled .md-icon, .review-add-video-button:disabled:hover .md-icon,
.review-edit-comment-button:disabled .md-icon, .review-edit-comment-button:disabled:hover .md-icon,
.review-delete-comment-button:disabled .md-icon, .review-delete-comment-button:disabled:hover .md-icon {
  color: rgba(0, 0, 0, .24);
}

.peer-review-grading-info {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 0 0 16px 0;
  padding: 0;

  .peer-review-grading-message {
    flex: 1;
  }

  .peer-review-grading-progress {
    display: flex;
    flex-direction: column;
    margin-left: 16px;

    .peer-review-grading-progress-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;

      .progress-item-label {
        margin: 0 4px 0 0;
      }

      .progress-item-icon {
        &.progress-item-icon--complete {
          color: themed-color('status-success');
        }

        &.progress-item-icon--incomplete {}
      }
    }
  }
}

.review-header {
  .review-header-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;

    .card-header {
      margin-right: 16px;
    }

    .review-header-right-container {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      flex: 1;
      flex-wrap: wrap;

      .review-header-progress-label {
        padding: 0 8px;
        white-space: nowrap;

        &.label--bold {
          font-weight: bold;
        }
      }

      .review-header-progress-icon {
        font-size: 24px;
      }

      .review-header-progress {
        flex: 1;
        max-width: 300px;
        min-width: 100px;
      }
    }
  }
}

.review-card {
  .review-content {
    padding: 16px;

    @media (max-width: 500px) {
      padding: 16px 8px 8px 8px;
    }
  }
}

.review-paper-container {
  display: flex;
  flex-direction: column;
  margin: 8px;

  .review-paper {
    display: flex;
    flex-direction: column;

    .review-paper-header {
      display: flex;
      flex-direction: column;
    }

    .review-paper-header-primary {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;
      padding: 12px;

      .review-student-select-container {
        display: flex;
        flex-direction: row;

        h5 {
          margin: 0;
          line-height: unset;
        }

        .review-student-select-field {
          margin: 0 0 0 8px;
          padding: 0;
          width: auto;

          .md-text-field--margin {
            margin: 0;
            height: auto;
          }

          .md-divider--text-field {
            margin: 4px 0 0 0;
          }
        }
      }

      .next-peer-button {
        margin: 0;
        padding: 0;
        height: 28px;
      }
    }

    .review-paper-header-secondary {
      display: flex;
      flex-direction: row;
      background-color: rgba(0, 0, 0, .2);
      padding: 0;

      @media (max-width: 400px) {
        flex-direction: column;
      }

      .review-progress-item {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex: 1;
        border-left: 1px solid rgba(255, 255, 255, .5);

        @media (max-width: 400px) {
          border-top: 1px solid rgba(255, 255, 255, .5);
          border-left: none;
        }

        padding: 4px;

        .review-progress-item-icon {
          margin-right: 4px;
        }

        .review-progress-item-label {
          font-weight: bold;
          text-transform: uppercase;
          font-size: 12px;
        }

        &.review-progress-item--complete {
          .review-progress-item-icon {
            color: themed-color('button-raised-primary-contrast');
          }

          .review-progress-item-label {
            color: themed-color('button-raised-primary-contrast');
          }

          background-color: themed-color('button-raised-primary-background');
        }
      }

      .review-progress-item:first-child {
        border: none;
      }
    }

    .review-paper-content {
      padding: 8px;
    }
  }
}

.peer-review-due-date-container {
  display: flex;
  align-items: center;
  margin-bottom: 16px;

  p {
    margin: 0 0 0 8px;
  }
}
