.c-notification {
  list-style: none;

  .c-notification__item {
    display: table;
    width: 100%;
    padding: 10px 5px;
    margin: 0 0 5px 0;
  }

  .c-notification__item.c-notification__item--unread {
    background-color: #fcffdc;
  }
  .c-notification__item-date {
    display: table-cell;
    padding: 0 20px 0 0;
    width: 135px;
  }
  .c-notification__item-text {
    display: table-cell;
    color: black;
    font-size: 1.2em;
  }

  .c-notification__title {

  }

  .c-notification__unread {
    font-size: .8em;
    color: #8e8e8e;
  }
}
