@import 'common/styles/theme.scss';

.eval-container {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.eval-comments {
    width: 100%;
}

.eval-rating-container {
    flex-grow: 1;
    margin-left: 48px;
}

.eval-name-container {
    align-items: center;
    flex: 1;
    font-size: 24px;
    padding: 0 0 0 8px;
    height: 70px;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    white-space: nowrap;
    overflow: hidden;
}

.eval-user-container {
    display: flex;
    flex-direction: row;   
    margin-left: -8px;
}

.eval-avatar-container {
    display: flex;
    height: 70px;
    align-items: center;
}

@media all and (max-width: 500px) {
    .eval-avatar-container {
        display: none;
    }
}

.eval-button-container {
    display: flex;
    justify-content: flex-end;
}

.eval-button-container button.md-text--theme-primary {
    color: themed-color('button-raised-primary-background');
}

.eval-button-container button.md-text--theme-secondary {
    color: themed-color('button-raised-secondary-background');
}

.eval-rubric-result-button {
    font-size: 16px;
    line-height: 16px;
    margin: 0 0 0 4px;
}

.eval-rubric-result-container {
    font-size: 18px;
    display: flex;
    align-items: center;
}

.eval-card {
    min-width: 300px;
    padding: 0;
}

.rubric-eval-myscore-container {
    display: flex;
    align-items: center;
    padding-left: 8px;
}

.rubric-eval-myscore-container button {
    min-width: 66px;
    margin-left: 4px;
}
