.c-qa-question {
  padding: 15px;
  display: flex;

  .c-qa-question__media {
    background-color: black;
    margin: 0 20px 0 0;
    width: 200px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .c-qa-question__add-video-button {
    color: white;
  }

  .c-qa-question__delete-container {
    display: flex;
    justify-content: center;
    flex-direction: row;
    padding: 0 10px;
  }

  .c-qa-question__action-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .c-qa-question__info-container {
    width: 100%;
  }

  .c-qa-question__dragger {
    display: flex;
  }
}
