.c-recorder .recording-timer-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.recording-timer {
  $icon-size: 20px;

  background-color: rgba(0, 0, 0, .75);
  padding: 6px 12px;
  border-radius: 0 0 12px 12px;
  display: flex;
  flex-direction: row;
  align-items: center;

  .recording-timer-icon {
    display: flex;
    margin-right: 4px;

    .recording-dot {
      font-size: $icon-size;
    }
  }

  .recording-timer-elapsed {
    color: white;
    font-size: 13px;
    font-weight: bold;
    line-height: $icon-size;
    margin-right: 8px;
  }

  .recording-timer-max {
    color: #ccc;
    font-size: 12px;
    line-height: $icon-size;
  }
}

.recording-dot {
  display: flex;

  @keyframes recording-active {
    from {
      opacity: 1;
    }
    to {
      opacity: .5;
    }
  }

  &.recording-dot--active {
    color: red;
    animation-name: recording-active;
    animation-duration: .75s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in;
  }
}
