@import 'common/styles/theme.scss';

.milestone-owners {
  display: table-cell;
}

.milestone-dialog {
  width: 500px;

  &.mobile-dialog {
    width: 100%;

    .milestone-form {
      padding: 16px;
    }
  }
}

.milestone-status-completed {
  color: themed-color('status-completed');
}

.milestone-status-open {
  color: themed-color('status-open');
}

.milestone-status-past-due {
  color: #f58e25;
}

.milestone-status-column-label {
  padding-right: 0 !important;
}

@media all and (max-width: 1100px) {
  .milestone-status-column-label {
    display: none;
  }
}

@media all and (max-width: 1000px) {
  .milestone-owners,
  .milestone-owners-header {
    display: none;
  }
}

@media all and (max-width: 740px) {
  .milestone-due-date,
  .milestone-due-date-header {
    display: none;
  }
}

.milestone-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.milestone-page-form {
  padding: 16px;
  flex: 1;
  overflow: auto;
}

.milestone-page-actions {
  padding: 16px;
  display: flex;
  justify-content: flex-end;
}

.owner-selection-container-label {
  display: block;
}

.owner-selection-chips {
  margin-top: 4px;
}

.milestone-datetime-message {
  margin: 8px 0;

  .status-message-content-text {
    font-weight: bold;
  }
}
