.quick-nav {
    display: flex;
    flex-grow: 1;
    align-items: center;
    margin: 0;
    padding-left: 16px;
    font-size: 18px;
    overflow: hidden;

    &.md-title {
        margin: 0;
        line-height: unset;
    }
   
    &.md-title--toolbar {
        margin: 0;
        line-height: unset;
    }

    .menu-button-container {
        display: flex;
        align-items: center;

        .quick-nav-menu-button {
            margin-left: 4px;
        }
    }

    .quick-nav-separator {
        margin: 4px;
    }

    .quick-nav-item {
        flex-shrink: 0;

        &.plain-text {
            flex-shrink: 1;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}

@media all and (max-width: 550px) {
    .quick-nav {
        display: none;
    }
}
