@import 'common/styles/theme.scss';

.combine-media-list {
  display: flex;
  flex-direction: column;
}

.combine-media-list-item-drag-container--active {
  list-style: none;
  background-color: #fff;
  outline-width: 2px;
  outline-offset: -2px; // Not supported on IE11
  outline-style: dashed;
  outline-color: themed-color('button-flat-primary-contrast');
}

.combine-media-list-checked-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;

  .combine-media-list-message {
    background-color: themed-color('button-raised-primary-background');
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

    p {
      color: themed-color('button-raised-primary-contrast');
      font-size: 14px;
      margin: 8px;
    }
  }

  .combine-media-list-checked-drag-container {
    padding: 8px;
  }
}
