@import 'common/styles/theme.scss';

.input-tooltip-container,
.input-select-tooltip-container {
  width: 100%;
}

.select-rubric-container {
  display: flex;
  align-items: center;
}

#rubric-dialog {
  width: auto;
}

.input-selection-control-group {
  .md-selection-control-container {
    height: auto;
  }
}

.input-percentage {
  textarea {
    text-align: center;
    overflow: hidden;
  }
}

.selection-control {
  // .selection-control, .selection-control-radio-checkbox, .selection-control-radio
  &.selection-control--selected {
    .md-fake-btn {
      @include themed-ink('selection-control-selected', 'md-ink-container');
      color: themed-color('selection-control-selected');
    }
  }
  &.selection-control--unselected {
    .md-fake-btn {
      // always use the "selected" / "contrast" color for the ink background
      @include themed-ink('selection-control-selected', 'md-ink-container');
      color: themed-color('selection-control-unselected');
    }
  }
  &.selection-control--disabled {
    .md-fake-btn {
      color: themed-color('selection-control-disabled');
    }
  }
}
