.column-avatar {
    display: flex;
    align-items: center;

    .avatar {
        height: 32px;
        width: 32px;

        * {
            font-size: 16px;
        }
    }

    .avatar-label {
        margin-left: 8px;
    }

}

.column-avatar {
    > div:first-child {
        margin-left: 0;
    }

    > div {
        margin-left: 2px;
    }
}

.column-menu-button ul {
    max-height: 400px;
    display: flex;
    flex-direction: column;

    .list-item {
        flex: 1;
    }
}

.column-menu-button .md-list-tile {
    height: 48px;
}

.column-menu-button .md-tile-content--left-icon {
    padding-left: 16px;
}

.column-menu-button .icon-container {
    padding-left: 0;
}

.column-menu-button .icon-container-label {
    padding-left: 8px;
}

.drop-down-arrow-icon {
    padding-left: 4px !important;
}

.column-menu-dialog-content .dialog-contents {
    padding: 0;
   
    .column-menu-dialog-list { }
}
