@import "common/styles/mixins.scss";

.add-video-dialog {
  width: 500px;
}

.add-video-dialog.full-page {
  width: 100%;
}

.add-video-checklist {
  @include clearfix;
  clear: both;
  display: inline-block;
  margin: 0;
  padding: 0;
  text-align: center;
}

.add-video-checkmark, .add-video-checkmark-btm {
  @include clearfix;
  clear: both;
  display: block;
  float: none;
  margin: 0;
  text-align: left;
}

.add-video-checkmark {
  padding: 5px 0 0 0;
}

.add-video-checkmark-btm {
  padding: 0;
}

.align-top, .align-top * {
 

  vertical-align: top !important;
}
