.interactive-video-work-in-progress {
   
    .interactive-video-card-body {
        display: flex;
        flex-direction: row;
        padding: 0;
    }

    .player-recorder-container, .question-container {
       flex: 1;
    }

    .question-container {
        display: flex;
        flex-direction: column;
        padding: 0 0 0 16px;
    }

    .question-container-header {
        h2 {
            margin: 0;
        }
    }

    .question-contents-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
        padding: 8px;

        &.multiple-choice {
            flex-direction: column;
            align-items: initial;
            flex: 1;
        }

        &.processing-message {
            i {
                margin-right: 8px;
            }
        }

        &.video-question {
            flex-direction: column;

            .question-text {
                display: flex;
                flex: 1;
                align-items: center;
            }

            .answer-actions {
                align-self: flex-end;
                flex: 0;
            }
        }

        .question-text {
            font-weight: bold;
            font-size: 16px;
            padding: 8px 0;
        }
    }

    .answer-actions {
        display: flex;
        justify-content: flex-end;
        flex: 1;
        align-items: flex-end;

        .retry-button {
            margin-right: 16px;
        }
    }

    .answer-feedback {
        display: inline-flex;
        align-items: center;
        padding: 8px;

        .feedback-icon {
            font-size: 24px;
            padding-right: 8px;
        }

        .feedback-text {
            font-size: 16px;
        }
    }

    .video-progress-container {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        padding: 16px;

        .video-progress-action-button {
            margin-right: 16px;
        }
    }

    @media all and (max-width: 1000px) {
        .interactive-video-card-body {
            flex-direction: column;
        }

        .question-container {
            padding: 16px 0 0 0;           
        }

        .question-contents-container {
            padding: 0;
            min-height: 100px;

            .question-text {
                padding: 16px 0;
            }
        }
    }

}
