
.create-groups-dialog-container {
  padding: 0 8px;
  flex: 1;

  .create-groups-dialog-header {
    margin-bottom: 8px;
    font-style: italic;
  }

  .create-groups-dialog-actions {
    margin: 8px 0;
    display: flex;
    flex-direction: column;

    .create-groups-action-button {
      margin: 0;
      height: auto;
      text-transform: unset;
      text-align: unset;
      justify-content: flex-start;
    
      .create-groups-action-button-container {
        display: flex;
        flex-direction: row;
    
        .create-groups-action-button-icon {
          display: flex;
          align-items: center;
          margin-right: 8px;
    
          .font-icon {
            font-size: 24px;
          }
        }
    
        .create-groups-action-button-contents {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding: 8px;
    
          .create-groups-action-button-title {
            font-size: 18px;
          }
        }
      }
    }
  }

  .create-groups-dialog-copy-actions {
    display: flex;
    justify-content: center;
    margin: 24px 0;
  }

  .create-groups-dialog-copy-footer {
    margin-top: 24px;
  }
}


