@import 'common/styles/mixins.scss';

$control-bar-height: 25px;

.c-recorder {
  .c-recorder__controls-bar {
    display: flex;
    align-items: stretch;
    height: $control-bar-height;
  }

  .c-recorder__controls-bar-icon {
    color: #545454;
  }

  .c-recorder__actions-container {
    display: flex;
    flex: 1;
    > * {
      display: flex;
      flex: 1;
    }
  }

  .c-recorder__playback-actions {
    display: flex;
    align-items: stretch;
    height: $control-bar-height;
    background-color: white;

    .c-recorder__action-btn {
      display: flex;
      flex: 1;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      height: 100%;

      &:hover {
        background-color: #d4d4d4;
      }
    }
  }
}
