@import 'common/styles/theme.scss';

.c-player__volume-changer {
  display: table-cell;
  width: 30px;
  position: relative;
  vertical-align: middle;
}

.c-player__volume-slider-holder {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateY(-100%) translateX(-50%);
  background-color: #f9f9f9;
  padding: 10px 2px 8px 2px;
  height: 80px;
}

.c-player__volume-icon {
  color: themed-color('page-contrast') !important;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.c-player__volume-icon.c-player__volume-icon--active {
  color: rgb(0, 188, 212) !important;
}
