.c-recorder {
  display: flex;
  flex-direction: column;
  max-height: 100vh;
  max-width: 100vw;

  &.c-recorder--standalone {
    height: 100vh;
    width: 100vw;
  }

  .recorder-main-container {
    display: flex;
    flex: 1;
    position: relative;
    overflow: hidden;
  }

  .c-recorder__placeholder {
    padding-top: 75%;
    background-color: #eeeeee;
    position: relative;
  }

  .c-recorder__placeholder-text {
    display: flex;
    color: #8e8e8e;
    font-size: 25px;
    position: absolute;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 16px;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    > div,
    > button {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .c-recorder__processing-progress {
    .md-circular-progress-path {
      stroke: #2679c7;
    }
  }

  .c-recorder__overlay-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 5px;
    font-size: 18px;
    background-color: rgba(0, 0, 0, .7);
    color: #d8d5d5;
    top: 0;
    left: 0;
  }
}

.recorder-dialog {
  display: flex;
  flex-direction: column;
  width: 70%;
  max-width: 800px;

  .dialog-contents {
    display: flex;
    flex: 1;
    padding: 0 16px 16px;

    @media (max-height: 500px) {
      .c-recorder .c-recorder__stream-holder {
        min-height: 100px;
      }
      .c-recorder .recorder-videos {
        min-height: 100px;
      }
    }

    &.full-page {
      background-color: black;
      display: flex;
      flex-direction: column;
      padding: 0;

      .c-recorder .recorder-video-wrapper {
        .recorder-video {
          max-height: calc(100vh - 103px);
        }
      }

      .c-recorder .c-recorder__stream {
        display: block;

        .c-recorder__stream-video {
          max-height: calc(100vh - 74px);
        }
      }
    }
  }

  .dialog-media {
    display: flex;
    flex: 1;
  }

  .recorder-content {
    display: flex;
    padding: 0;
    overflow: hidden;
  }

  .c-recorder {
    flex: 1;
  }
}

.recorder-dialog.md-dialog--full-page {
  width: 100%;
}
