@import 'common/styles/theme.scss';

.toolbar {
    align-items: center;

    background-color: themed-color('page-toolbar-background');
    color: themed-color('page-toolbar-contrast');

    h2 {
        margin: 8px 0;
        white-space: normal;
    }

    a, .quick-nav, .quick-nav-item, .quick-nav-separator {
        color: themed-color('page-toolbar-contrast');
    }

    .button.button-flat {
        @include themed-button('page-toolbar-button');
    }

    // a:disabled, button:disabled {
    //     color: rgba(255, 255, 255, .38);
    // }

    .md-toolbar--action-right {
        display: flex;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
}

