.documents-dialog {
    width: auto;
}

.document-list-container {
    max-height: 450px;
    overflow: auto;

    .document-list-item {
        padding: 8px 0;
        display: flex;
        align-items: center;
        min-width: 200px;
    }

    .document-list-item-title {
        flex: 1;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }

    .partial-type-icon {
        padding-right: 8px;
    }
}

.documents-dialog {

    &.full-page {
        width: 100%;
    }
   
    .dialog-contents.full-page {
        display: flex;
        flex-direction: column;

        .document-list-container {
            padding: 16px;
        }
    }
}
