.notification-container {
  .notification-item {
    padding: 0;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12), 0 3px 1px -2px rgba(0, 0, 0, .2);

    .notification-item-body {
      margin: 0;
      display: flex;
    }
  }
}

body.bongo-ios {
  max-width: 100vw;
  // -webkit-text-size-adjust: none; // TODO: Evaluate future use of these to prevent automatic text scaling on iOS devices
}

::-moz-focus-inner {
  border: 0;
}

:invalid {
  box-shadow: none;
}
