@import 'common/styles/theme.scss';

.meeting-dialog {
    width: 500px;

    .meeting-dialog-errors {
        margin-top: 16px;

        p {
            margin: 0;
            color: inherit;
        }
    }

    &.mobile-dialog {
        width: 100%;

        .meeting-form {
            padding: 16px;
        }
    }
}

.meeting-page {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.meeting-page-form {
    padding: 16px;
    flex: 1;
    overflow: auto;
}

.meeting-page-actions {
    padding: 16px;
    display: flex;
    justify-content: flex-end;
}

.meeting-table .font-icon {
    color: themed-color('page-contrast');
}

.meeting-table .md-text--disabled .font-icon {
  color: #8e8e8e;
  cursor: default;
  &:hover {
    color: #8e8e8e;
  }
}
