@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.md-list-item .md-list-tile {
  height: 48px;
}

.md-tile-content.md-tile-content--left-icon {
  padding-left: 16px;
}

button {
  &.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding-top: 6px;
    padding-bottom: 6px;
  }
}

button.button[disabled] {
  // workaround for Chrome issue where mouse(leave|enter) events are not fired correctly on a disabled element (eg. <button/>)
  pointer-events: none;
}

.button {
  &.button-raised,
  &.button-floating {
    &.button-base {
      @include themed-button('button-raised', 'base');
    }

    &.button-primary {
      @include themed-button('button-raised', 'primary');
    }

    &.button-secondary {
      @include themed-button('button-raised', 'secondary');
    }
  }

  &.button-flat,
  &.button-icon {
    &.button-base {
      @include themed-button('button-flat', 'base');
    }

    &.button-primary {
      @include themed-button('button-flat', 'primary');
    }

    &.button-secondary {
      @include themed-button('button-flat', 'secondary');
    }
  }
}
