@import 'common/styles/bongo-vars.scss';

.activity-auto-analysis-desired-key-term {
  margin: 6px 0;

  .activity-auto-analysis-desired-key-term-container {
    display: flex;
    flex-direction: row;
    align-items: flex-end;

    .activity-auto-analysis-desired-key-term-input-label {
      white-space: nowrap;
      margin-right: 8px;
    }

    .activity-auto-analysis-desired-key-term-input {
      margin-right: 16px;
    }

    .activity-auto-analysis-desired-key-term-item-actions {
      display: flex;
      align-items: center;
    }
  }

  .activity-auto-analysis-desired-key-term-count {
    width: 50px;
    min-width: 50px;
    margin-right: 8px;

    input {
      text-align: center;
    }
  }

  .activity-auto-analysis-key-term-validation-container {
    display: flex;
    align-items: center;
    margin-top: 2px;
  }

  .activity-auto-analysis-key-term-validation-icon {
    color: $bongo-color-avatars-red;
    font-size: 24px;
    margin-right: 8px;
  }
}
