.qa-answer-dialog {
    width: auto;
}

.qa-answer-video-placeholder, .qa-answer-media-player {
    width: 100%;
    min-height: 120px;
    min-width: 120px;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
}

.qa-answer-video-placeholder {
    .font-icon {
        color: white;
        font-size: 24px;
    }

    .crashed {
        color: red;
    }
}

.qa-answer-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.qa-answer-dialog-contents .qa-answer-container:not(:first-child) {
    margin-top: 16px;
}

.qa-answer-info-container {
    padding: 16px;
}

.qa-answer-info-item {
    .info-label {
        margin-right: 4px;
    }

    .withdrawn-comment {
        padding-left: 8px;
    }
}

.qa-answer-info-item span {
    white-space: nowrap;
}
