@import 'common/styles/theme.scss';

.peer-review-submission-assets-container {
  box-shadow: none;
  border-right: 1px solid #ccc;
}

.peer-review-rubric-assessment-container {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  margin-bottom: 4px;
}

.peer-review-card {
  .peer-review-no-submissions {
    padding: 16px;
  }

  .peer-review-content {
    padding: 16px;

    @media (max-width: 500px) {
      padding: 16px 8px 8px 8px;
    }
  }

  .has-review * {
    color: green;
  }

  .peer-review-header {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;

    &.no-reviews {
      display: block;
    }

    .peer-review-header-title {
      display: flex;
      flex: 1;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;

      .card-header {
        white-space: nowrap;
        margin: 0;
        align-self: center;
      }
  
      .peer-review-header-right-container {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex: 1;
        flex-wrap: wrap;
  
        .peer-review-header-progress-label {
          padding: 0 8px;
          white-space: nowrap;
  
          &.label--bold {
            font-weight: bold;
          }
        }
  
        .peer-review-header-progress-icon {
          font-size: 24px;
        }
  
        .peer-review-header-progress {
          flex: 1;
          max-width: 300px;
          min-width: 100px;
        }
      }
    }

    @media all and (max-width: 800px) {
      .peer-review-header-title {
        .card-header {
          flex: 0;
          margin-right: 16px;
          align-self: flex-start;
        }
      }
    }
  }

  .peer-review-rating-container {
    display: flex;
    padding-bottom: 6px;
    justify-content: center;

    .input-rating {
      margin: 0 auto;
    }
  } 
}

.peer-review-grading-info {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 0 0 16px 0;
  padding: 0;

  .peer-review-grading-message {
    flex: 1;
  }

  .peer-review-grading-progress {
    display: flex;
    flex-direction: column;
    margin-left: 16px;

    .peer-review-grading-progress-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;

      .progress-item-label {
        margin: 0 4px 0 0;
      }

      .progress-item-icon {
        &.progress-item-icon--complete {
          color: themed-color('status-success');
        }

        &.progress-item-icon--incomplete {}
      }
    }
  }
}

.peer-review-due-date-container {
  display: flex;
  align-items: center;
  margin-bottom: 16px;

  p {
    margin: 0 0 0 8px;
  }
}

.peer-review-paper-container {
  display: flex;
  flex-direction: column;
  margin: 8px;

  .peer-review-paper {
    display: flex;
    flex-direction: column;

    .peer-review-paper-header {
      display: flex;
      flex-direction: column;
    }

    .submission-review-container {
      margin-bottom: 8px;
    }

    .peer-review-paper-header-primary {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;
      padding: 12px;

      .peer-review-student-select-container {
        display: flex;
        flex-direction: row;

        h5 {
          margin: 0;
          line-height: unset;
        }

        .peer-review-student-select-field {
          margin: 0 0 0 8px;
          padding: 0;
          width: auto;

          .md-text-field--margin {
            margin: 0;
            height: auto;
          }

          .md-divider--text-field {
            margin: 4px 0 0 0;
          }
        }
      }

      .next-peer-button {
        margin: 0;
        padding: 0;
        height: 28px;
      }
    }

    .peer-review-paper-header-secondary {
      display: flex;
      flex-direction: row;
      background-color: rgba(0, 0, 0, 0.2);
      padding: 0;

      @media (max-width: 400px) {
        flex-direction: column;
      }

      .peer-review-progress-item {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex: 1;
        border-left: 1px solid rgba(255, 255, 255, 0.5);

        @media (max-width: 400px) {
          border-top: 1px solid rgba(255, 255, 255, 0.5);
          border-left: none;
        }

        padding: 4px;

        .peer-review-progress-item-icon {
          margin-right: 4px;
        }

        .peer-review-progress-item-label {
          font-weight: bold;
          text-transform: uppercase;
          font-size: 12px;
        }

        &.peer-review-progress-item--complete {
          .peer-review-progress-item-icon {
            color: themed-color('button-raised-primary-contrast');
          }

          .peer-review-progress-item-label {
            color: themed-color('button-raised-primary-contrast');
          }

          background-color: themed-color('button-raised-primary-background');
        }
      }

      .peer-review-progress-item:first-child {
        border: none;
      }
    }

    .peer-review-paper-content {
      padding: 8px;
    }
  }

  @media (max-width: 500px) {
    margin: 0;
  }
}
