@import 'common/styles/theme.scss';

.c-recorder {
  .c-recorder__stream-holder {
    display: flex;
    flex: 1;
    background-color: #222;
    min-height: 200px;
    position: relative;
    overflow: hidden;
  }

  .c-recorder__video-placeholder {
    width: 100%;
    padding-top: 75%;
    background-color: #222;
  }

  .c-recorder__stream {
    display: flex;
    flex: 1;
    position: relative;
    overflow: hidden;
  }

  .c-recorder__stream-video {
    flex: 1;
    max-height: 100%;
    max-width: 100%;
  }

  .c-recorder__stream-audio {
    width: 100%;
    height: 100%;
    min-height: 300px;
    background: linear-gradient(to right, #83a4d4, #b6fbff);
    opacity: .7;
    position: relative;
    overflow: hidden;
  }

  .c-recorder__sound-canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  .c-recorder__audio-description {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    font-size: 28px;
    color: rgba(0, 0, 0, .5);
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  .recorder-screen-chooser-wrapper {
    position: absolute;
    right: 0;
    bottom: 0;

    .recorder-screen-chooser {
      margin: 4px;
      width: 36px;
      height: 36px;
    }
  }
}

.recorder-overlay-container {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .recorder-activating-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    flex: 1;
    background: rgba(0, 0, 0, .5);
    padding: 8px;
  }

  .recorder-activating-message {
    display: flex;
    align-items: center;
    margin: 16px 0;

    .recorder-activating-message-loader {
      margin-right: 8px;
      --bongo-spinner-size: 24px;
      --bongo-spinner-thickness: 3px;
    }

    .recorder-activating-message-text {
      font-size: 20px;
      color: #ddd;
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: 1px;
    }
  }

  // extra specificity needed here
  .recorder-activating-banner .recorder-standalone-launcher {
    $color: #ccc;
    $color-disabled: #ccc;
    @include create-button-skin(#0008, $color, #0008, $color-disabled);
    @include create-ink-skins($color);

    border: 1px solid #aaa;
    border-radius: 8px;
    padding: 6px 12px;
    margin-top: 8px;
    box-sizing: border-box;
    height: auto;
    width: auto;

    .font-icon {
      margin-right: 4px;
    }

    .recorder-standalone-launcher-content {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .recorder-standalone-launcher-content > span {
      display: flex;
      align-items: center;
      margin-bottom: 4px;
    }

    .recorder-standalone-launcher-content > span:last-child {
      margin-bottom: 0;
    }

    .recorder-standalone-launcher-text {
      color: #ccc;
      font-style: italic;
      text-transform: none;
    }
  }
}
