.c-recorder {
  // Should match .c-recorder__stream-holder
  .recorder-videos {
    display: flex;
    flex: 1;
    background-color: #222;
    min-height: 200px;
    position: relative;
    overflow: hidden;
  }

  // Should match .c-recorder__stream
  .recorder-video-wrapper {
    display: flex;
    flex: 1;
    position: relative;
    overflow: hidden;

    &.recorder-video-wrapper--hidden {
      display: none;
    }
  }

  // Should match .c-recorder__stream-video
  .recorder-video {
    flex: 1;
    max-height: 100%;
    max-width: 100%;
  }
}
