@import 'common/styles/theme.scss';

$item-primary-action-color: themed-color('selection-control-selected');
$x-small-breakpoint: 480px;
$small-breakpoint: 700px;

.submission-content-card {
  section {
    &.md-card-text {
      @media (max-width: $small-breakpoint) {
        padding: 8px;
      }

      @media (max-width: $x-small-breakpoint) {
        padding: 0;
      }
    }
  }

  .content-card-container {
    display: flex;
    flex-direction: column;
    padding: 8px;

    .content-card-container--header {
      display: flex;
    }
  }

  .content-card-container--note {
    margin-bottom: 16px;
  }

  .content-card-container--message {
    display: flex;
    align-items: center;
    justify-content: center;

    .content-card-container--message-wrapper {
      display: flex;
      border: 2px dashed #757575;
      padding: 12px;
      border-radius: 16px;
    }

    .content-card-container--message-label {
      div {
        display: flex;
        margin: 2px 0 0 0;
        &:first-child {
          margin: 0;
        }

        .font-icon {
          font-size: 24px;
          vertical-align: text-bottom;
          color: $item-primary-action-color;
        }
      }
    }
    .content-card-container--message-icon {
      font-size: 48px;
      margin-right: 16px;
    }
  }

  .content-card-container--footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 16px 0 8px 0;
    justify-content: flex-start;
  }

  .content-card-container--footer-label {
    margin-top: 16px;

    span {
      font-weight: bold;
      font-size: 20px;
      margin: 0 4px;
    }
  }

  .content-card-container--footer-button {
    justify-self: flex-end;
  }

  .content-card-container--included {
    margin-top: 16px;
  }
}
