.c-preview-instruction-link-dialog {
  max-width: 800px;
  width: 95%;

  .c-preview-url {
    flex: 1;
    width: 100%;

    iframe {
      height: 100%;
      width: 100%;
      box-shadow: inset 0 0 10px rgba(0, 0, 0, .4);
      background-color: transparent;
    }
  }

  .dialog-title {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .dialog-contents {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
