@import 'common/styles/theme.scss';

.bongo-main {
  header.toolbar {
    $trial-button-background-name: 'page-toolbar-button-contrast';
    $trial-button-contrast-name: 'page-toolbar-background';
    .toolbar-trial-button {
      @include create-button-skin(
        themed-color($trial-button-background-name),
        themed-color($trial-button-contrast-name),
        themed-color($trial-button-background-name),
        themed-color($trial-button-contrast-name)
      );
      @include create-ink-skins(themed-color($trial-button-contrast-name));

      border-radius: 8px;
      margin-right: 8px;
      font-weight: bold;
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 0 12px;
    }
  }
}
