@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.speed-dial-button {
  position: fixed;
  margin: 0;
  top: auto;
  right: 20px;
  bottom: 20px;
  left: auto;
  z-index: 15;
  display: flex;
}

.speed-dial-button-option {
  left: 4px;
}

@media (max-width: $bongo-desktop-min-width) {
  .speed-dial-button-option {
    left: 8px;
  }
}

.spinner-container {
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;

  .spinner-icon-container {
    display: flex;
    position: absolute;
    transition: all 450ms;

    .font-icon {
      color: inherit;
    }

    &.spinner-icon-container--closed {
      &.spinner-icon-container--visible {
        transform: rotate(0);
        opacity: 1;
      }

      &.spinner-icon-container--hidden {
        transform: rotate(90deg);
        opacity: 0;
      }
    }

    &.spinner-icon-container--open {
      &.spinner-icon-container--visible {
        transform: rotate(0);
        opacity: 1;
      }

      &.spinner-icon-container--hidden {
        transform: rotate(-90deg);
        opacity: 0;
      }
    }
  }
}
