@import './styles.scss';

.auto-analysis-report-container {
  width: 100%;
}

.auto-analysis-report {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex: 1;

  .auto-analysis-report-info {
    display: flex;
    flex-direction: column;

    .auto-analysis-report-info-title {
      font-weight: bold;
      font-size: 16px;
      margin: 0;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: center;
    }

    .auto-analysis-report-info-items {
      display: flex;

      &.auto-analysis-report-info-items--inline {
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        margin-top: 4px;

        > * {
          margin: 0 12px;
          flex-shrink: 0;
        }
      }

      &.auto-analysis-report-info-items--stacked {
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        > * {
          margin: 0 0 4px 0;
          flex-shrink: 0;

          &:last-child {
            margin: 0;
          }
        }
      }
    }

    .auto-analysis-report-info-item {
      display: flex;
      align-items: flex-end;
      flex-wrap: wrap;
      min-height: 20px;

      .auto-analysis-report-info-item-label {
        margin-right: 4px;
        font-size: 12px;
        text-transform: uppercase;
      }

      .auto-analysis-report-info-item-value {
        font-weight: bold;
        font-size: 13px;
      }
    }
  }

  .auto-analysis-report-overall-container {
    display: flex;
    margin: 8px 0 0 0;
    justify-content: center;

    > * {
      margin: 0 8px;
    }
  }

  .auto-analysis-report-overall-gauge-container {
    display: flex;
    flex: 1;
    max-width: 150px;
    min-width: 125px;
    align-self: center;
  }

  .auto-analysis-report-overall-gauge {
    flex: 1;

    .auto-analysis-report-overall-gauge-threshold-c:before {
      background-color: $analysis-rating-color-c;
    }
    .auto-analysis-report-overall-gauge-threshold-b:before {
      background-color: $analysis-rating-color-b;
    }
    .auto-analysis-report-overall-gauge-threshold-a:before {
      background-color: $analysis-rating-color-a;
    }
  }

  .auto-analysis-report-summary-cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 8px 0;

    > * {
      margin: 8px;
    }
  }
}

.auto-analysis-report--unclear {
  display: flex;
  flex-direction: column;
  align-items: center;

  .auto-analysis-report-status-message-unclear-container {
    display: flex;
    flex-direction: row;

    .auto-analysis-report-status-message-unclear-container--left {
      display: flex;
      align-items: center;
      margin-right: 32px;
    }

    .auto-analysis-report-status-message-unclear-container--right {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }

    .auto-analysis-report-status-message-unclear-icon {
      font-size: 96px;
      color: rgba(0, 0, 0, .35);
    }
  }

  .auto-analysis-report-status-message-unclear--primary {
    margin: 0;

    p {
      font-size: 16px;
      text-transform: uppercase;
      font-weight: 700;
      margin: 0;
    }
  }

  .auto-analysis-report-status-message-unclear--secondary {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    p {
      margin: 4px 0 0 0;
    }
  }

  .auto-analysis-report-status-message-unclear--actions {
    display: flex;
    align-items: center;
    margin-top: 24px;
  }
}
