.general-section-form {
    margin: 0 0 24px 0;
}

.general-section-form h2 {
    margin: 0;
}

@media all and (max-width: 700px) {
  .general-section-form {
      display: block;
      width: 100%;
  }
 
  .general-section-form {
      width: 100%;
  }
 
  .general-section-form label, .general-section-form input {
      white-space: normal !important;
      max-width: 100%;
  }
 
  .general-section-form input {
      line-height: 2.5;
      min-height: 1px !important;
      height: auto !important;
  }
}

.grade-type-container {
    display: flex;
    flex-flow: row wrap;

    .grade-type-selectfield {
        min-width: 150px;
    }

    .grade-type-selectfield-container {
        flex: 1;
    }

    .grade-type-auto-analysis-source-selectfield-container {
        flex: 1;
        margin-left: 8px;
    }

    .grade-type-select-rubric-container {
        flex: 1;
        margin-left: 8px;
    }
}

.grade-type-tooltip-container {
    flex: 1;
    padding-right: 8px;
}



.assignment-datetime-message {
    margin: 8px 0 16px 0;
   
    .status-message-content-text {
        font-weight: bold;
    }
}

.grade-type-option--disabled:hover {
    background-color: rgba(0, 0, 0, .05);
}

.grade-type-option-tooltip {
    width: 100%;
}
