@import 'common/styles/mixins.scss';
@import 'common/styles/theme.scss';

.content-card,
.md-card {
  display: block;
  margin: 0 auto 20px auto;
  padding: 0;
  position: relative;
}

.content-card {
  @include clearfix;
  clear: both;
  display: block;
  min-height: 1px;
  height: auto;
  width: auto;
  margin: 0 auto 20px auto;
  position: relative;
  padding: 16px;
  @media (max-width: 500px) {
    padding: 12px;
  }
  background: themed-color('surface-background');
}

.card-body {
  padding: 0 0 16px 0;
}

.card-placeholder-text {
  font-style: italic;
  font-size: 14px;
}

.md-card-title.card-title {
  padding: 0;
  display: flex;
  align-items: center;

  .card-title-left-content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    flex: 1;

    .card-title-left-content-text {
      h2 {
        flex-grow: 1;
        margin-right: 8px;
      }
    }

    .card-title-left-content-children {
    }
  }

  .card-title-left-content {
    .card-header {
      @media screen and (max-width: 400px) {
        font-size: 22px;
      }

      @media screen and (max-width: 320px) {
        font-size: 20px;
      }
    }
  }

  .card-title-right-content .card-title-actions {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 8px;

    > div {
      margin-top: 0;
      margin-bottom: 0;
      margin-right: 8px;
    }

    > div:last-child {
      margin-right: 0;
    }
  }
}

.card-actions {
  padding: 0 16px 16px 16px;
  justify-content: center;
}

@media screen and (max-width: 320px) {
  .card-placeholder-text {
    font-size: 16px;
  }
}
