@import 'common/styles/theme.scss';

.c-player__play-button {
  display: table-cell;
  width: 30px;
  text-align: center;
  position: relative;
}

.c-player__play-button-icon {
  font-size: 20px;
  width: 100%;

  color: themed-color('page-contrast') !important;
  line-height: 25px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);

  &:hover {
    color: rgb(0, 188, 212) !important;
  }
}
