$highlightColor: rgba(255, 255, 0, .4);

.auto-analysis-transcript-contents-transcript > .auto-analysis-transcript-paragraph:first-child {
  margin-top: 0;
}

.auto-analysis-transcript-container-context--clarity {
  .auto-analysis-transcript-fragment--unclear {
    background-color: $highlightColor;
  }
}

.auto-analysis-transcript-container-context--fillers {
  .auto-analysis-transcript-fragment--filler {
    background-color: $highlightColor;
  }
}

.auto-analysis-transcript-container-context--key-terms {
  .auto-analysis-transcript-fragment--key-term {
    background-color: $highlightColor;
  }
}

.auto-analysis-transcript-container-context--top-n {
  .auto-analysis-transcript-fragment--topn{
    background-color: $highlightColor;
  }
}

.auto-analysis-transcript-paragraph {
  display: block;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: .1px;
  color: rgba(0, 0, 0, .87);
  -webkit-text-size-adjust: none;
}
