.bongo-gauge {
  .bongo-gauge-track-container {
    position: relative;
  }

  .bongo-gauge-track {
    --bongo-gauge-track-thickness-px: 32px;
    padding-bottom: 50%;
    position: relative;
    width: 100%;
    height: 0;
    overflow: hidden;

    &:before {
      content: '';
      position: absolute;
      height: 200%;
      width: 100%;
      border-radius: 50%;
      background-color: #dddddd;
    }

    &:after {
      content: '';
      background: white;
      position: absolute;
      top: var(--bongo-gauge-track-thickness-px);
      right: var(--bongo-gauge-track-thickness-px);
      bottom: calc(calc(100% - var(--bongo-gauge-track-thickness-px)) * -1);
      left: var(--bongo-gauge-track-thickness-px);
      border-radius: 50%;
      -webkit-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, .25);
      -moz-box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, .25);
      box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, .25);
    }

    .bongo-gauge-track-fill {
      position: absolute;
      width: 100%;
      height: 100%;
      overflow: hidden;
      transform-origin: center bottom;

      // --bongo-gauge-rotation should be expressed in 'turn' units where 0.50turn = 100% (0.25 = 50%)
      --bongo-gauge-rotation: 0turn;
      transition: all 1s;
      transform: rotate(calc(0.5turn + var(--bongo-gauge-rotation)));

      &:before {
        content: '';
        position: absolute;
        height: 200%;
        width: 100%;
        border-radius: 50%;
        // the color should always be set by "threshold" class, do not default here to avoid specificity battles
        // background-color: #bbbbbb;
      }

      &.bongo-gauge-threshold-unknown:before {
        background-color: #bbbbbb;
      }
    }
  }

  .bongo-gauge-value-label {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50%;
    font-weight: bold;
    font-size: 28px;
    line-height: 1;
    padding-top: 5%;
  }

  .bongo-gauge-label {
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
  }
}
