@import "common/styles/bongo-vars.scss";

.composite-grade-form-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;

  h3, h4, h5, h6 {
    margin: 0;
  }
}

.composite-grade-form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
  padding: 16px;

  .composite-grade-form-row-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 16px;
  }

  .composite-grade-form-row-container:last-child {
    margin-bottom: 0;
  }

  .composite-grade-form-row-separator {
    height: 1px;
    background: $bongo-color-secondary-gray-light;
    width: 100%;
  }

  .composite-grade-form-row-label {
    display: flex;
    justify-content: flex-end;
    flex-shrink: 0;
    flex-grow: 1;
    margin: 0 12px 0 0;
  }

  .composite-grade-form-row-value-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 96px;

    &.composite-grade-form-fivestar-container {
      width: auto;

      .input-rating {
        line-height: 24px;
      }
    }

    &.composite-grade-form-passfail-container {
    }

    .composite-grade-form-row-value-input {
      flex-shrink: 0;
      width: 100%;
      text-align: center;

      .md-text-field-multiline-container {
        height: auto !important;
        min-height: 18px;

        > textarea {
          height: auto !important;
        }
      }

      .md-text-field--margin {
        margin: 4px 0;
        display: flex;
      }

      .md-divider {
        margin: 0;
      }

      .md-select-field--text-field {
        min-height: 18px;
       
        .md-icon-text {
          padding: 0 4px;
          text-align: center;
        }
      }
    }

    .composite-grade-form-row-value-footer {
      i {
        font-size: 11px;
        color: $bongo-color-secondary-gray-dark;
      }
    }

    .composite-grade-form-row-value-footer-error {
      i {
        color: $bongo-color-avatars-red;
      }
    }
  }
}
