@import "common/styles/bongo-vars.scss";
@import 'common/styles/theme.scss';

.reflection-questions-work-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.reflection-questions-work-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;

  .reflection-questions-work-message {
    margin-bottom: 16px;
  }

  .reflection-questions-work-list {
    width: 100%;
    padding: 8px;
  }
}

.reflection-questions-work-completed-container {
  display: flex;
  flex-direction: row;
  align-items: center;

  .reflection-questions-work-completed-icon {
    color: $bongo-color-avatars-green;
    margin-left: 8px;
  }
}
