.member-avatar-container {
    display: flex;
    align-items: center;
}

.member-avatar-container .member-name-label {
    padding-left: 6px;
}

.group-members-actions {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.group-members-actions-left {
    flex: 1;
}

.rating-column-renderer {
    display: flex;
    align-items: center;
}

.rating-column button {
    min-width: 60px;
    margin-left: 4px;
}

.rubric-eval-nav-container {
    display: flex;
    padding-bottom: 8px;
}

.rubric-eval-contents {
    flex-grow: 1;
    padding: 0 16px 16px 16px;
}

.rubric-eval-user-info {
    display: flex;
}

.rubric-eval-user-avatar {
    display: flex;
    height: 60px;
    -ms-flex-align: center;
    align-items: center;
}

.rubric-eval-user-details {
    font-size: 24px;
    padding: 0 0 0 6px;
    height: 60px;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.rubric-eval-comment-container {
    display: flex;
    align-items: center;
}

.rubric-eval-comment-container * {
    font-size: 13px;
}

.rubric-eval-comment-container p {
    margin: 0 0 0 8px;
}
