@import 'common/styles/theme.scss';

.group-members-dashboard {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.group-members-content {
  flex-grow: 1;
}

.group-members-dashboard-card .dashboard-card-actions {
  justify-content: flex-start;
}

.fyi-text-long, .fyi-text-med {
  clear: both;
  display: block !important;
  float: none !important;
  margin: 0 auto !important;
}

.fyi-text-long {
  font-size: 18px;
}

.fyi-text-med {
  font-size: 30px;
  padding: 0;
}

.eval-nav-container {
  display: flex;
  height: 100%;
  flex-direction: row;
  padding: 4px 0;
}

.eval-nav-contents {
  flex-grow: 1;
  margin-left: -10px; // need this because the buttons in the card take too much space. This makes it look better
  margin-right: -10px;
}

.eval-card {
  padding: 0;
  min-height: 150px;
}

.eval-left-icon, .eval-right-icon {
  align-self: center;
}

.group-submitted-dashboard-container {
  display: flex;
  flex-direction: row;
  padding: 0 0 16px 0;
}

.group-submitted-dashboard-container .dashboard-card:last-child {
  margin-right: 0 !important;
}

@media all and (max-width: 1180px) {
  .group-submitted-dashboard-container .dashboard-card:nth-child(2) {
    margin-right: 0;
  }

  .group-submitted-dashboard-container .dashboard-card:nth-child(3), .group-submitted-dashboard-container .dashboard-card:nth-child(4) {
    display: none;
  }
}

@media all and (max-width: 900px) {
  .group-submitted-dashboard-container {
    flex-direction: column;

    .dashboard-card:not(:first-child) {
      margin-top: 16px;
    }
  }

  .group-submitted-dashboard-container .dashboard-card {
    margin: 0;
    flex: 0;
  }
}

.group-submitted-dashboard-container .avatar {
  margin: 0 4px 0 0 !important;
}

.group-submitted-dashboard-container button.md-text--theme-primary {
  color: themed-color('button-raised-primary-background');
}

.group-submitted-dashboard-container button.md-text--theme-secondary {
  color: themed-color('button-raised-secondary-background');
}
