@import 'common/styles/theme.scss';

.recorder-overlay-container {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .recorder-activating-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    flex: 1;
    background: rgba(0, 0, 0, .5);
    padding: 8px;
  }

  .recorder-activating-message {
    display: flex;
    align-items: center;
    margin: 16px 0;

    .recorder-activating-message-loader {
      margin-right: 8px;
      --bongo-spinner-size: 24px;
      --bongo-spinner-thickness: 3px;
    }

    .recorder-activating-message-text {
      font-size: 20px;
      color: #ddd;
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: 1px;
    }
  }

  // extra specificity needed here
  .recorder-activating-banner .recorder-standalone-launcher {
    $border-color: #aaa;
    $background-color: #0008;
    $contrast-color: #ccc;
    $contrast-color-disabled: #ccc;
    @include create-button-skin($background-color, $contrast-color, $background-color, $contrast-color-disabled);
    @include create-ink-skins($contrast-color);

    border: 1px solid $border-color;
    border-radius: 8px;
    padding: 6px 12px;
    margin-top: 8px;
    box-sizing: border-box;
    height: auto;
    width: auto;

    .font-icon {
      margin-right: 4px;
    }

    .recorder-standalone-launcher-content {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .recorder-standalone-launcher-content > span {
      display: flex;
      align-items: center;
      margin-bottom: 4px;
    }

    .recorder-standalone-launcher-content > span:last-child {
      margin-bottom: 0;
    }

    .recorder-standalone-launcher-text {
      color: $contrast-color;
      font-style: italic;
      text-transform: none;
    }
  }

  .recorder-screen-chooser-wrapper {
    position: absolute;
    right: 0;
    bottom: 0;

    .recorder-screen-chooser {
      margin: 4px;
      width: 36px;
      height: 36px;
    }
  }
}
