.c-cloud-transfer {
  text-align: center;

  .c-cloud-transfer__button {
    cursor: pointer;
    width: 33%;
    display: inline-block;
    text-align: center;
  }

  .c-cloud-transfer__button.c-cloud-transfer__button--disabled {
    opacity: .2;
    cursor: default;
  }

  .c-cloud-transfer__image {
    display: block;
    margin: 0 auto;
    max-width: 100%;
    height: auto;
  }
  .c-cloud-transfer__name {
    font-size: 13px;
  }

  .c-cloud-transfer__onedrive-iframe {
    width: 33%;
    display: inline-block;
    height: 77px;
    border: none;
  }

}

body {
  .picker-dialog {
    position: fixed;
  }
}
