@import 'common/styles/theme.scss';

.bongo-note-container {
  $icon-size: 20px;
  $icon-gap: 8px;
  $background-opacity: .1;

  --note-theme-color-contrast: #{themed-color('status-unknown')};
  --note-theme-color-background: #{themed-color('status-unknown', $background-opacity)};

  &.bongo-note--info {
    --note-theme-color-contrast: #{themed-color('status-info')};
    --note-theme-color-background: #{themed-color('status-info', $background-opacity)};
  }

  &.bongo-note--warning {
    --note-theme-color-contrast: #{themed-color('status-warning')};
    --note-theme-color-background: #{themed-color('status-warning', $background-opacity)};
  }

  &.bongo-note--error {
    --note-theme-color-contrast: #{themed-color('status-error')};
    --note-theme-color-background: #{themed-color('status-error', $background-opacity)};
  }

  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: white;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border-left: 4px solid var(--note-theme-color-contrast);

  .bongo-note-content {
    display: flex;
    flex: 1;
    padding: 8px;
    background-color: var(--note-theme-color-background);
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  .bongo-note-title-icon .font-icon {
    display: flex;
    color: var(--note-theme-color-contrast);
    width: $icon-size;
    height: $icon-size;
    margin-right: $icon-gap;
  }

  .bongo-note-text-container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex: 1;
    overflow: hidden;
    color: var(--note-theme-color-contrast);

    .bongo-note-text-title {
      color: var(--note-theme-color-contrast);
      text-transform: uppercase;
      font-size: 16px;
      font-weight: bold;
      line-height: $icon-size;
    }

    .bongo-note-text-message {
      color: var(--note-theme-color-contrast);
      font-size: 14px;
      overflow-wrap: break-word;
    }
  }
}
