@import "common/styles/mixins.scss";

.data-table {
  .sortable-header {
    cursor: pointer;
  }
}

.table-column-list-wrap, .table-column-list {
  @include clearfix;
  clear: both;
  display: block;
  float: none;
  min-height: 1px;
  height: auto !important;
  height: 1px;
}

.table-column-list-wrap {
  clear: both;
  float: none;
}

.table-column-list {
  /*clear: none ;
  float: left;*/
}

@media all and (max-width: 700px) {
  .table-column-list {
    clear: both;
    float: none;
  }
}