@import 'common/styles/theme.scss';

@mixin dropzone-active {
  button, button > i {
    color: themed-color('button-flat-primary-contrast');
  }

  background-color: themed-color('button-flat-primary-contrast', .1);
}

.c-file-button {
  display: inline-block;
  border: none;
  height: auto;
  width: auto;

  .c-file-button__file-name {
    margin: 0 0 0 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 150px;
    display: inline-block;
  }

  &.file-button-drop-area--active {
    @include dropzone-active;
  }
}

.button-file-upload {
  display: flex;

  > i {
    margin-right: 4px;
  }
}
