@import 'common/styles/theme.scss';

.button-trial {
  .button-trial--icon {
    margin-right: 8px;
  }

  .button-trial--label-time {
    &.button-trial--label-time--severity-0 {
      color: themed-color('status-info');
    }

    &.button-trial--label-time--severity-1 {
      color: themed-color('status-warning');
    }

    &.button-trial--label-time--severity-2 {
      color: themed-color('status-error');
    }
  }
}

.tooltip-trial--content {
  display: flex;
  flex-direction: column;
  padding: 4px 0;

  .tooltip-trial--header {
    display: flex;
    justify-content: center;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 16px;
    margin-bottom: 4px;
  }

  .tooltip-trial--message-container > div {
    display: flex;
    align-items: flex-start;
    margin-top: 8px;
  }

  .tooltip-trial--message--centered {
    justify-content: center;
  }

  .tooltip-trial--message-icon {
    color: rgba(255, 255, 255, 1);
    margin-right: 6px;
  }

  .tooltip-trial--message-actions {
    .tooltip-trial--message-button {
      @include themed-button('tooltip-button');
      // This themed button is a little bit weird because it is a flat button (transparent bg), on a dark tooltip
      // (so the contrast needs to be white instead of normal gray'ish text)
      margin-right: 6px;
    }

    .tooltip-trial--message-button:last-child {
      margin-right: 0;
    }
  }
}

.trial-card-detail-container > div {
  display: flex;
  align-items: flex-start;
  margin-top: 8px;
  min-height: 24px;
}

.trial-card-detail-icon--left {
  display: inline-flex;
  margin-right: 6px;
}

.trial-card-detail-icon--right {
  display: inline-flex;
  margin-left: 4px;
}

.trial-card-detail-text {
  display: flex;
  flex-wrap: wrap;
}

.trial-card-detail-button {
  display: flex;
}
