@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.reflection-questions-settings-container {
  margin-top: 16px;

  .reflection-questions-settings-header {
    display: flex;
    align-items: center;

    .reflection-questions-settings-header-left {
      margin: 0;
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;

      .reflection-questions-settings-header-text--primary {
        margin: 0;
      }

      .reflection-questions-settings-header-text--secondary {
        margin: 0 0 0 8px;
      }
    }
  }

  .reflection-questions-settings {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px;

    .reflection-questions-settings-header {
      display: flex;
      align-items: center;
      border-bottom: 1px solid $bongo-color-secondary-gray-light;
      margin-bottom: 8px;

      h4 {
        display: inline-block;
        margin: 0 8px 0 0;
      }
    }

    .reflection-questions-settings-message {
      margin: 0;
    }

    .reflection-questions-settings-message--warning {
      margin-bottom: 8px;
    }

    .reflection-questions-settings-list {
      width: 100%;
      padding: 8px;

      .reflection-questions-settings-list-item-drag-container--active {
        outline-color: themed-color('button-raised-primary-background');
        background-color: #fff;
        outline-width: 2px;
        outline-offset: -2px; // Not supported on IE11
        outline-style: dashed;
      }
    }

    .reflection-questions-settings-actions {
      display: flex;
      justify-content: center;
    }
  }

  .reflection-questions-settings-validation-container {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
    max-width: 600px;

    .reflection-questions-settings-validation-icon {
      color: $bongo-color-avatars-red;
      font-size: 24px;
    }

    .reflection-questions-settings-validation-errors {
      flex: 1;
      display: flex;
      flex-direction: column;

      h5 {
        flex: 1;
        color: $bongo-color-avatars-red;
        margin: 0 0 0 8px;
      }
    }
  }
}
