.c-calendar {
  .c-calendar__delete-button-wrapper:focus {
    outline: 1px solid white;
  }
  .c-calendar__delete-button {
    position: absolute;
    right: 2px;
    bottom: 2px;
    color: white;

    i {
      font-size: 17px;
      color: white;
    }
  }

  .c-calendar__delete-button:hover,
  .c-calendar__delete-button:focus {
    i {
      color: #2f2f2f;
    }
  }

  .rbc-addons-dnd .rbc-addons-dnd-resize-ns-anchor {
    height: 5px;
  }
}