.auto-analysis-key-terms-configure {
  padding: 8px 10px;
  height: auto;
  font-weight: bold;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .auto-analysis-key-terms-configure-label-primary {
    display: flex;
    align-items: center;
    align-self: flex-start;
  }
  .auto-analysis-key-terms-configure-label-secondary {
    display: flex;
    align-items: center;
    align-self: flex-end;

    font-weight: normal;
    font-style: italic;
    text-transform: none;
    margin-left: 30px;
  }

  .auto-analysis-key-terms-configure-icon {
    margin: 0 10px 0 0;
  }
}

.auto-analysis-key-terms-configure-note {
  margin-left: 6px;
}
