.c-system-groups {
  .c-system-groups__total-time {
    display: flex;
    align-items: center;
    padding: 10px 0;
  }
  .c-system-groups__total-time--valid {
    i, span {
      color: #609460;
    }
  }

  .c-system-groups__total-time--invalid {
    i, span {
      color: tomato;
    }
  }

  .c-system-groups__total-time-text {
    padding: 0 0 0 5px;
    font-weight: bold;
  }
}