@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.comment-presets-dialog {
    width: 70%;
    max-width: 550px;
    min-width: 400px;
}

.comment-wrapper {
    overflow: auto;
    flex: 1;
}

.comment-item {
    display: flex;
    align-items: center;
    padding : 16px;

    span {
        flex: 1;
        white-space: pre-wrap;
        word-break: break-word;
        max-height: 50px;
        overflow-y: hidden;
    }
}

.comment-presets-content {
    display: flex;
    flex-direction: column;
}

.comment-item.delete-message {
    color: themed-color('status-info');
    font-weight: bold;
    font-size: 14px;
}
