@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';
@import './SubmissionReviewVars.scss';

@keyframes show-submission-video {
  from {
    width: 0;
    height: 0;
  }
  to {
    width: 100%;
    height: $fixed-content-height;
  }
}

@keyframes hide-submission-work-items {
  0% {
    opacity: 1;
  }
  99% {
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

.submission-review-assets-container {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 8px;
  max-height: $fixed-content-height;
  min-height: $fixed-content-height;

  .submission-review-asset-items {
    overflow-y: auto;

    .content-list-item {
      border: none;
      border-bottom: 1px solid $border-color;
      padding: 8px 0 8px 2px;

      &:first-child {
        border-top: none;
      }
    }
  }

  &.meeting-playback,
  &.dual-stream-playback,
  &.video-playback {
    .post-submission-asset-items {
      overflow-y: auto;

      .content-list-item {
        animation: $animation-period linear forwards hide-submission-work-items $animation-repeat;
      }
    }
  }

  .submission-review-video-player-container {
    .submission-review-video-player-close-button {
      position: absolute;
      top: -16px;
      right: -16px;
      color: white;
      z-index: 2;
      background-color: black;
      border: 2px solid white;
    }
  
    &.animate-playback {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      background-color: black;
      animation: $animation-period linear forwards show-submission-video $animation-repeat;
  
      &.meeting-playback {
        display: flex;
        align-items: center;
      }
  
      &.dual-stream-playback .c-double-player__stream-holder--main {
        .video-js {
          height: $fixed-content-height;
          padding: 0;
        }
      }
  
      &.dual-stream-playback .c-double-player__stream-holder--active {
        .video-js {
          height: unset;
          min-height: 140px;
        }
      }
  
      &.video-playback .video-js {
        height: $fixed-content-height;
        padding: 0;
      }
    }
  }
}


