@import 'common/styles/theme.scss';

.auto-analysis-transcript-contents-word-list {
  margin-right: 16px;

  &.auto-analysis-transcript-contents-word-list--width-small {
    margin-right: 0;
  }
}

.auto-analysis-transcript-word-list-container {
  padding: 8px;

  .auto-analysis-transcript-word-list {
    width: 200px;
    display: flex;
    flex-direction: column;

    .auto-analysis-transcript-word-list-header {
      display: flex;
      flex-direction: row;
      margin: 8px 0;
      font-weight: bold;
    }

    .auto-analysis-transcript-word-list-header-label {
      margin: 0;
      font-size: 14px;
      letter-spacing: unset;
      line-height: unset;
      white-space: normal;
    }
  }

  .auto-analysis-transcript-word-list-sep {
    height: 2px;
    background-color: rgba(0, 0, 0, .3);
  }

  .auto-analysis-transcript-word-list-contents {
    margin-top: 8px;

    .word-list-item:last-child {
      border-bottom: none;
    }
  }
}

.auto-analysis-transcript-contents-small-width {
  .auto-analysis-transcript-contents-word-list--width-small {
    margin-right: 0;
    display: flex;
    justify-content: flex-end;
  }
}

.auto-analysis-transcript-word-list-drawer-open-button {
  .md-icon {
    font-size: 28px;
  }
}

.auto-analysis-transcript-word-list-drawer {
  header {
    position: relative;
  }

  .word-list-item {
    padding: 16px 16px 16px 8px;
  }
}

.word-list-item--empty-container {
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-style: italic;
}

.word-list-item {
  padding: 8px 8px 8px 4px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-left: 4px solid transparent;
  border-bottom: 1px solid #eee;
  border-radius: 2px;
  position: relative;
  @include create-ink-skins(#555, 'md-ink-container');

  .word-list-item-selection {
    width: 4px;
  }

  &.word-list-item--selected {
    border-left: 4px solid themed-color('button-raised-secondary-background');
  }

  .word-list-item-label {
    flex: 1;
    font-size: 14px;
    word-break: break-word;
  }

  .word-list-item-value-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 6px;

    .word-list-item-value-units {
      text-transform: uppercase;
      font-size: 10px;
      font-weight: bold;
      color: rgba(0, 0, 0, .5);
    }
  }
}
