@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

$item-primary-action-color: themed-color('selection-control-selected');

.content-list-container {
  .new-content-list-item {
    margin-bottom: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;

    &:hover {
      background: none;
      cursor: auto;
    }
  }

  .new-content-list-item--default {
    &:focus-within {
      background-color: rgba(0, 0, 0, 0);
    }

    .md-ink-container {
      .md-ink--active {
        background-color: #fff;
      }
    }

    .new-content-list-item--add-content-button-tooltip {
      flex: 1;
    }

    .new-content-list-item--add-content-button {
      $base-color: #757575;

      flex: 1;
      height: auto;

      border-color: $base-color;
      border-style: dashed;
      border-width: 4px;
      border-radius: 16px;

      &[disabled] {
        opacity: 0.5;
      }

      &:hover,
      &:focus {
        border-color: currentColor;

        .new-content-list-item--text,
        .new-content-list-item--icon {
          color: inherit;
        }
      }

      .new-content-list-item--icon {
        font-size: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 16px;
        color: $base-color;
      }

      .new-content-list-item--text {
        font-size: 18px;
        font-weight: bold;
        letter-spacing: 2px;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: $base-color;
      }
    }
  }

  .new-content-list-item--chooser {
    flex-direction: column;
    align-items: stretch;
  }

  .new-content-list-item-header {
    $background: themed-color('page-toolbar-background');
    $contrast: themed-color('page-toolbar-contrast');
    $contrast-disabled: #ccc;

    display: flex;
    align-items: center;
    background-color: $background;
    color: $contrast;

    .new-content-list-item-header--label {
      flex: 1;
      padding: 8px;
      margin: 0;
      color: $contrast;
    }

    .new-content-list-item-header--actions {
      display: flex;

      .new-content-list-item-header--actions-close {
        @include create-button-skin(transparent, $contrast, transparent, $contrast-disabled);
        @include create-ink-skins($contrast);
      }
    }
  }

  .new-content-list-item-main {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex: 1;

    .new-content-list-item-main--chooser {
      display: flex;
      flex-direction: column;
      flex: 1;

      .new-content-list-item-main--chooser-row {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex: 1;
        flex-wrap: wrap;

        .new-content-list-item-main--chooser-button {
          height: unset;
          flex-direction: column;
          padding: 8px;
          margin: 0;
          flex: 1;
          max-width: 150px;

          .font-icon {
            font-size: 32px;
          }
        }
      }
    }
  }

  .new-content-list-item-footer {
    position: absolute;
    bottom: 0;

    .new-content-list-item-footer--label {
      text-transform: uppercase;
      font-weight: bold;
      font-size: 20px;
    }
  }
}
