.c-player__transcript-button {
  display: table-cell;
  width: 30px;
  position: relative;
  vertical-align: middle;
}

.c-player__transcript-icon {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  font-size: 14px;

  &:hover {
    color: rgb(0, 188, 212);
  }
}

.c-player__transcript-icon.c-player__transcript-icon--active {
  color: rgb(0, 188, 212);
}
