@import 'common/styles/theme.scss';

.review-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.review-video-container {
  display: flex;
  flex: 1;
  // min-width is required to allow proper width calculation for flex property
  min-width: 0;
  padding: 0;
  flex-direction: column;
  justify-content: flex-start;
  border: 1px solid #666;
  background-color: #666;

  .c-player {
    align-self: center;
  }
}

.review-submit-section {
  display: flex;

  .review-add-video-button {
    margin-right: 4px;
  }
}

.review-document-container {
  flex: 1;
  padding: 16px;
  border-right: 1px solid #eee;
  min-height: 300px;

  &.no-media {
    padding: 8px;
  }
}

.review-comments-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-self: stretch;

  &.no-media {
    padding: 8px;
  }
}

.review-feed-container {
  height: 100%;
  max-height: 450px;
  overflow-y: scroll;
  padding: 16px;
  flex: 1;
}

.review-submit-container {
  padding: 0 0 0 16px;
}

.review-feed {
  flex: 1;
  flex-direction: column-reverse;
}

.review-textfield {
  flex: 1;
}

.review-student-select-field {
  padding: 0 14px;
  width: auto;
}

.review-header {
  display: flex;
  align-items: center;
  padding: 0;
}

.review-header h2 {
  white-space: nowrap;
  margin: 0;
  align-self: center;
}

.review-header-title {
  flex: 1;
  display: flex;
  align-items: center;
}

@media all and (max-width: 800px) {
  .review-header-title {
    flex-direction: column;
    align-items: flex-start;
  }

  .review-header-title h2 {
    align-self: flex-start;
  }
}

.review-header.no-reviews {
  display: block;
}

.review-rating-container {
  display: flex;
  padding-bottom: 6px;
  justify-content: center;
}

.review-rating-container .input-rating {
  margin: 0 auto;
}

.review-wrapper {
  .video-top-toolbar-container {
    height: 48px;
    width: 100%;
    background-color: #666;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .video-actions-menu-button {
      $color: #fff;
      $color-disabled: #ccc;
      @include create-button-skin(
        transparent,
        $color,
        transparent,
        $color-disabled
      );
      @include create-ink-skins($color);
    }
  }
}

.edit-peer-review-item-dialog {
  width: auto;
}

.review-card .has-review * {
  color: green;
}

.review-card-actions {
  justify-content: flex-end;
}

.review-no-completed-reviews {
  clear: none;
  color: rgba(0, 0, 0, .87);
  float: left;
  margin: 0 !important;
  padding: 24px 0 0 0;
  position: static;
}

@media all and (max-width: 800px) {
  .review-no-completed-reviews {
    clear: both;
    padding: 16px 0 0 16px;
  }
}

.review-no-submissions {
  padding: 16px;
}

.review-feed.no-comments {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media all and (max-width: 1000px) {
  .review-wrapper {
    flex-direction: column;
    align-items: stretch;
  }

  .review-video-container {
    max-width: 100%;
  }

  .review-document-container {
    &.no-media {
      border-right: none;
    }
  }

  .review-comments-container {
    padding: 0;
    &.no-media {
      border-top: 1px solid #eee;
    }
  }

  .review-submit-container {
    padding: 0;
  }
}
