@import './SubmissionReviewVars.scss';

@keyframes grow-submission-work-items {
  from {
    min-height: 100px;
  }
  to {
    min-height: $fixed-content-height;
  }
}

.submission-review-container {
  margin-top: 8px;
  display: flex;
  flex-direction: row;
}

@media all and (max-width: 900px) {
  .submission-review-container {
    flex-direction: column;
    align-items: stretch;

    .submission-review-assets-container {
      border: 1px solid $border-color;
      padding: 4px;
      min-height: unset;
  
      &.video-playback,
      &.dual-stream-playback {
        animation: $animation-period linear forwards grow-submission-work-items $animation-repeat;
      }
  
      &.meeting-playback {
        max-height: unset;
  
        .content-list-item {
          display: none;
        }
      }
  
      .submission-review-asset-items {
        .content-list-item {
          padding: 8px;
  
          &:last-child {
            border-bottom: none;
          }
        }
      }
  
      .submission-review-video-player-container {
        &.meeting-playback {
          display: unset;
          position: static;
        }
      }
    }

    .submission-review-comments-container {
      margin-top: 16px;
      min-height: 200px;
      padding: 0;

      .submission-review-comments {
        min-height: unset;
        padding: 8px;
      }
    }
  }
}