.c-player {
  width: 100%;
  position: relative;
  overflow: hidden;
  &:fullscreen {
    height: 100%;
    .video-js, .c-player__video-wrapper {
      width: 100% !important;
      height: 100% !important;
      padding-top: 0 !important;
    }
  }

  &:hover {
    .c-player__controls {
      transform: translateY(0);
    }
  }

  .c-player__video-wrapper:focus + .c-player__controls {
    transform: translateY(0);
  }
}

.c-player--full-screen-view {
  height: 100%;
  .video-js, .c-player__video-wrapper {
    width: 100% !important;
    height: 100% !important;
    padding-top: 0 !important;
  }
}

.player-floating-actions-container {
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  padding: 4px;
}