.post-submission-card {
  .post-submission-card-header {
    align-items: center;
    flex-wrap: wrap;
    margin: 0;

    h2 {
      margin: 0;
      flex: 1;
    }
  }

  .post-submission-feedback-rating-container {
    margin: 0 auto;
    padding-bottom: 6px;

    .post-submission-fivestar-container {
      display: flex;
      align-items: center;
    }

    .post-submission-fivestar-container span {
      margin-left: 8px;
      font-size: 14px;
    }

    .post-submission-rubric-result-button {
      font-size: 16px;
      line-height: 16px;
      margin: 0 0 0 4px;
    }

    .post-submission-rubric-result-container {
      font-size: 18px;
      display: flex;
      align-items: center;
    }
  }
}
