@import '../styles.scss';

.auto-analysis-summary-card {
  display: flex;
  flex-direction: column;
  padding: 0;
  flex: 1;
  max-width: 400px;
  min-width: 250px;

  .auto-analysis-summary-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 4px 8px;
    color: white;
    line-height: 24px;

    .auto-analysis-summary-score {
      font-size: 24px;
      font-weight: bold;
      line-height: inherit;
      margin-right: 16px;
    }
    .auto-analysis-summary-title {
      font-size: 18px;
      font-weight: bold;
      line-height: inherit;
      text-transform: uppercase;
      color: white;
      margin-bottom: 0;
      flex: 1;
      letter-spacing: 2px;
    }

    .auto-analysis-summary-info-icon {
      color: white;
    }
  }

  .auto-analysis-summary-body {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    padding: 2px 8px;

    > * {
      margin: 6px 0;
      flex: 1;
    }
  }

  .auto-analysis-summary-item {
    display: flex;
    align-items: stretch;
    flex: 1;

    > * {
      margin-right: 4px;
      &:last-child {
        margin-right: 0;
      }
    }

    .auto-analysis-summary-item-score {
      display: flex;
      flex: 0;
      // align-items: center;
      margin-right: 8px;

      > * {
        margin-right: 4px;
        &:last-child {
          margin-right: 0;
        }
      }
    }

    .auto-analysis-summary-item-score-indicator {
      width: 8px;
    }

    .auto-analysis-summary-item-score-value {
      font-size: 16px;
      font-weight: bold;
      width: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .auto-analysis-summary-item-info {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
    }

    .auto-analysis-summary-item-title {
      font-size: 14px;
      font-weight: bold;
      line-height: 1;
      text-transform: uppercase;
      margin: 0 0 3px 0;
      color: #757575;
      letter-spacing: .5px;
    }

    .auto-analysis-summary-item-description {
      line-height: 1;
      color: #757575;
    }

    .auto-analysis-summary-item-chart-container {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      flex: 1;
    }

    .auto-analysis-summary-item-chart {
      flex: 1;
      max-width: 150px;
      min-width: 50px;
      position: relative;
      display: flex;
      height: 12px;
      margin: 8px 4px;
    }

    .auto-analysis-summary-item-chart-line {
      position: absolute;
      height: 100%;
    }
    .auto-analysis-summary-item-chart-arrow {
      position: absolute;
      height: 16px;
      width: 16px;
      line-height: 16px;
      transform: rotate(90deg);

      .font-icon {
        position: absolute;
        left: -12px;
        font-size: 16px;
        line-height: 16px;
        top: 8px;
        color: black;
      }
    }
  }
}

.auto-analysis-score-rating-background-color--a {
  background-color: $analysis-rating-color-a;
}

.auto-analysis-score-rating-background-color--b {
  background-color: $analysis-rating-color-b;
}

.auto-analysis-score-rating-background-color--c {
  background-color: $analysis-rating-color-c;
}

.auto-analysis-score-rating-background-color--unknown {
  background-color: $analysis-rating-color-unknown;
}
