.back-to-educator-button {
  position: fixed;
  top: 18px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 16;
  padding: 5px 10px;
  cursor: pointer;
  border: none;
  background-color: #FFCB20;
  border-radius: 3px;

  i {
    font-size: 13px!important;
    position: relative;
    top: 2px;
    left: -2px;
  }

  &:hover, &:focus {
    background-color: #FFDF77;
  }

  &:active {
    background-color: #FFC300;
  }
}
