@import 'common/styles/bongo-vars.scss';
@import 'common/styles/theme.scss';

.status-circle {
    background: themed-color('button-raised-primary-background');
    width: 200px;
    height: 200px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 64px;
    letter-spacing: 4px;
    color: white;
    font-weight: bold;
}

.error-page-container {
    height: 100vh !important;
    display: flex;
    flex-direction: column;
}

.error-page-content {
    display: flex;
    flex: 1;
    flex-direction: row;
}

.error-page-status-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
}

.error-page-information {
    display: flex;
    flex-direction: column;
    margin-left: 40px;
    height: 200px;
    align-items: center;
    justify-content: center;
}

.error-page-information span {
    font-size: 24px;
}

@media all and (max-width: 700px) {
    .error-page-content {
        flex-direction: column;
    }

    .error-page-information {
        margin-left: 0;
    }
}
