@import 'common/styles/theme.scss';

.wip-type-icon {
  color: themed-color('page-contrast');
  vertical-align: top;

  &.status-crashed {
    color: themed-color('status-warning');
  }
}

.wip-name-label {
  vertical-align: top;
  line-height: 25px;
  padding-left: 8px;
}

.wip-table .font-icon {
  color: themed-color('page-contrast');
}

.wip-table .md-text--disabled .font-icon {
  color: #8e8e8e;
  cursor: default;

  &:hover {
    color: #8e8e8e;
  }
}

.table-column-progress-icon {
  display: inline;
  margin: 0;
}

.submit-requested-info {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.submit-requested-info span {
  margin-left: 8px;
}

.submit-requested-info i {
  font-size: 24px;
}
