// This approach is used in leui of "inkDisabled=true"
// This removes the transition/animation but still keeps the ink container and children for accessibility
$md-ink-enter-transition-time: 0s;
$md-ink-leave-transition-time: 0s;

$md-card-padding-extra: 16px;
$md-data-table-padding: 8px;
$md-data-table-min-padding: 36px;

$md-text-field-divider-top-padding: 4px;
$md-text-field-line-height: normal;

$md-toolbar-mobile-portrait-height: $bongo-toolbar-height;
$md-toolbar-mobile-landscape-height: $bongo-toolbar-height;
$md-toolbar-tablet-height: $bongo-toolbar-height;

$md-font-icon-size: $bongo-font-icon-size;
$md-font-icon-dense-size: $bongo-font-icon-size;
$md-btn-icon-size: $bongo-button-icon-size;
$md-btn-icon-padding: $bongo-button-icon-padding;

$md-desktop-min-width: $bongo-desktop-min-width;

// If we don't override this, react-md uses an indigo color
// However, any react-md components that use this variable (which is a lot)
// will not receive proper theming.
$md-primary-color: $bongo-color-blue;

$md-font-family: themed-style('page-font-family');

$md-light-theme-text-color: $bongo-primary-text-color;
//$md-dialog-use-flex-positioning: true; // Would be nice to use flex position, but it breaks tons of react-md components when in a dialog

// $md-primary-color: $bongo-color-blue; // react-md usage of $md-primary-color cannot correctly support a css-var()
.bongo-main {
  .md-toolbar {
    background-color: themed-color('page-toolbar-background');
    color: themed-color('page-toolbar-contrast');

    .md-title--toolbar {
      color: themed-color('page-toolbar-contrast');
    }
  }

  .md-background--primary {
    background: themed-color('button-raised-primary-background');
    color: themed-color('button-raised-primary-contrast');
  }

  .md-background--secondary {
    background: themed-color('button-raised-secondary-background');
    color: themed-color('button-raised-secondary-contrast');
  }

  .md-divider--text-field::after {
    background: themed-color('button-raised-primary-background');
  }

  .md-text--theme-primary {
    color: themed-color('button-raised-primary-background');
  }

  .md-text--theme-secondary {
    color: themed-color('button-raised-secondary-background');
  }

  .md-picker-header {
    background: themed-color('button-raised-primary-background');
  }

  .md-clock-hand {
    background: themed-color('button-raised-primary-background');
    &::before,
    &::after {
      background: themed-color('button-raised-primary-background');
    }
  }

  .md-calendar-date--btn::after {
    background: themed-color('button-raised-primary-background');
  }

  .md-overlay {
    background: themed-color('page-overlay-background');
  }

  // TODO: Determine if we need this high-level override. Currently the themed-button(...) skinner overrides this
  // .md-circular-progress-path {
  //   stroke: themed-color('...');
  // }
}
