.reflection-questions-work-list-item {
  display: flex;
  align-items: flex-start;
  font-size: 14px;

  .reflection-questions-work-list-item-index {
    margin: 0 8px 0 0;
    width: 24px;
    display: flex;
    justify-content: flex-end;
    font-size: 14px;
  }

  .reflection-questions-work-list-item-qa {
    display: flex;
    flex-direction: column;
    flex: 1;

    .reflection-questions-work-list-item-question {
      margin-bottom: 8px;
      font-weight: bold;
    }

    .reflection-questions-work-list-item-answer {
      display: flex;
      flex-direction: row;

      .reflection-questions-work-list-item-answer-label {
        margin-right: 8px;
      }

      .reflection-questions-work-list-item-answer-input {
        .md-text-field-multiline-container.md-text-field--margin {
          margin: 0;
        }

        .md-text-field-multiline-container + .md-divider--text-field {
          margin-top: 0;
        }
      }

      .reflection-questions-work-list-item-answer-text {
        margin-bottom: 16px;
      }
    }
  }
}

.reflection-answers-dialog-container {
  width: 80%;
  max-width: 600px;

  &.full-page {
    width: 100vw;
    max-width: none;
  }
}

.reflection-answers {
  padding: 16px;
}
