.video-player-play-button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  cursor: pointer;
  background-color: rgba(0, 0, 0, .54);
  border-radius: 16px;
  padding: 0 16px;

  .video-player-play-button--icon {
    &.material-icons.md-icon {
      font-size: 96px;
    }

    color: rgba(white, .9);
  }

  &:hover {
    background-color: rgba(0, 0, 0, .75);

    .video-player-play-button--icon {
      color: rgba(white, 1);
    }
  }
}
