.attendance-dialog {
  width: 750px;
}

.card-top-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 16px;

  .search-container {
    max-width: 250px;
    flex: 1;
    border-radius: 20px;
    padding: 0 16px;
  }

  .search-input {
    max-width: 250px;

    .search-input-clear-button {
      width: 20px;
      height: 20px;
      padding: 0;
      top: unset;
      align-self: center;
    }

    .md-text-field-divider-container {
      margin-left: 4px;
    }

    .md-text-field-icon--positioned {
      align-self: center;
      margin-bottom: 0;
    }

    hr {
      visibility: hidden;
      margin-bottom: 4px;
    }
  }
}
